import { Injectable, Type } from '@angular/core';

import { Command, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../../structure/command/structure-aggregate.repository';
import { SetSchemaThemeCommand } from './set-schema-theme.command';
import { SchemaThemeSetEvent } from './schema-theme-set.event';
import { StructureAggregate } from '../../../structure/command/structure.aggregate';

@Injectable()
export class SetSchemaThemeCommandHandler implements CommandHandler<StructureAggregate, SetSchemaThemeCommand> {

	constructor(private structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetSchemaThemeCommand> {
		return SetSchemaThemeCommand;
	}

	handleAggregate(structure: StructureAggregate, command: SetSchemaThemeCommand): void {

		const theme = command.getTheme();

		structure.setTheme(theme);
	}

	publishDomainEvents(aggregate: StructureAggregate, command: Command): void {
		this.domainEventPublisher.publish(new SchemaThemeSetEvent(command.getAggregateId()));
	}

}
