import { Injectable } from '@angular/core';

import { PagingDispatcher } from '../../domain/paging/command/paging.dispatcher';
import { StructureId } from '../../domain/structure-id';
import { globalStructureId } from '../../domain/global-structure-id';
import { PagingConfig } from '../../domain/paging/paging-config';


@Injectable()
export class PagingCommandService {

	constructor(private pagingDispatcher: PagingDispatcher) {
	}

	enable(structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.setPaging(structureId, { enabled: true });
	}

	disable(structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.setPaging(structureId, { enabled: false });
	}

	setPaging(paging: PagingConfig, structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.setPaging(structureId, paging);
	}

	changePageSize(pageSize: number, structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.changePageSize(structureId, pageSize);
	}

	nextPage(structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.nextPage(structureId);
	}

	prevPage(structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.prevPage(structureId);
	}

	goToPage(pageNumber: number, currentPage: number) {

		if (currentPage < pageNumber) {
			while (currentPage !== pageNumber) {
				this.nextPage();
				currentPage += 1;
			}
		} else {
			while (currentPage !== pageNumber) {
				this.prevPage();
				currentPage -= 1;
			}
		}
	}

	changePagerTop(enabled: boolean, structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.setPaging(structureId, { pagerTop: enabled });
	}

	changePagerBottom(enabled: boolean, structureId: StructureId = globalStructureId): void {
		this.pagingDispatcher.setPaging(structureId, { pagerBottom: enabled });
	}
}
