import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Injector, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { CellTemplateWithContext } from '../../../../composition/domain/read/definition/cell-template-with-context';
import { StructureCommandService } from '../../../ui-api/structure/structure-command.service';
import { StructureColumnConfigService } from '../menu/structure.column-config.service';
import { SortOrder } from '../../../../composition/domain/command/column/sort/sort-order';
import { SmartComponent } from '../../../../../common/cdk/smart-component';


@Component({
	selector: 'gui-structure-header-columns',
	templateUrl: './structure-header-columns.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	providers: [
		StructureColumnConfigService
	]
})
export class StructureHeaderColumnsComponent extends SmartComponent implements OnInit {

	@Input()
	columns: Array<CellTemplateWithContext>;

	globalSearching = false;

	constructor(private elementRef: ElementRef,
				private injector: Injector,
				private changeDetectorRef: ChangeDetectorRef,
				private structureCommandService: StructureCommandService) {
		super();
	}

	ngOnInit() {
		// 	this.structureSearchReadModelRepository
		// 		.onSearchEnabled()
		// 		.pipe(
		// 			this.takeUntil()
		// 		)
		// 		.subscribe((searching: boolean) => {
		// 			this.globalSearching = searching;
		// 			this.changeDetectorRef.detectChanges();
		// 		});
	}

	toggleSort(column: CellTemplateWithContext): void {

		if (column.isSortEnabled()) {
			this.structureCommandService.toggleSort(column.getFieldId());
		}
	}

	isSortAsc(column: CellTemplateWithContext): boolean {
		return column.getSortStatus() === SortOrder.ASC;
	}

	isSortDesc(column: CellTemplateWithContext): boolean {
		return column.getSortStatus() === SortOrder.DESC;
	}

	isGlobalSortEnabled(): boolean {
		return this.globalSearching;
	}

}
