import { Injectable } from '@angular/core';

import { StructureAggregationPanelConfig } from './structure.aggregation-panel.config';

import { AggregationConfig } from '../../../ui-api/structure/aggregation/aggregation-config';


@Injectable()
export class StructureAggregationPanelConfigConverter {

	convert(config: AggregationConfig): StructureAggregationPanelConfig {

		if (config.enabled) {
			return new StructureAggregationPanelConfig(config.top, config.bottom);
		} else {
			return new StructureAggregationPanelConfig(false, false);
		}
	}
}
