import { FieldId } from './field.id';
import { DataType } from './data-type';
import { OriginItemEntity } from '../../../../source/command/origin/origin-item-entity';
import { AggregationType } from '../../aggregation/aggregation.type';
import { ColumnAggregationConfig } from '../../../../../../composition/domain/column-aggregation.config';
import { FieldMatchers } from '../matcher/field.matchers';
import { BaseField } from './base.field';

export abstract class Field<T> extends BaseField {

	private aggregationEnabled: boolean = true;

	private aggregations: AggregationType;

	private readonly possibleAggregations: AggregationType;

	protected constructor(id: FieldId,
						  accessor: any,
						  dataType: DataType,
						  matchers: FieldMatchers,
						  aggregationConfig?: ColumnAggregationConfig) {
		super(id,
			accessor,
			dataType,
			matchers);

		this.possibleAggregations = this.assignPossibleAggregations();

		this.assignAggregations(aggregationConfig);
	}

	abstract assignDefaultAggregations(): AggregationType;

	abstract assignPossibleAggregations(): AggregationType;

	abstract search(item: OriginItemEntity, searchPhrase: string): boolean;

	abstract sort(one: T, two: T): number;

	abstract filter(value: T, filterPhrase: T): boolean;

	isAggregation(type: AggregationType): boolean {

		if (!this.isAggregationEnabled()) {
			return false;
		}

		return !!(this.aggregations & type);
	}

	isAggregationEnabled(): boolean {
		return this.aggregationEnabled;
	}

	setAggregationEnabled(enabled: boolean): void {
		this.aggregationEnabled = enabled;
	}

	private assignAggregations(aggregationConfig: ColumnAggregationConfig): void {

		if (!!aggregationConfig && Object.keys(aggregationConfig).length !== 0) {

			if (aggregationConfig.enabled) {
				this.aggregationEnabled = !!aggregationConfig.enabled;
			}

			if (aggregationConfig.aggregationTypes) {

				this.aggregations = 0;

				aggregationConfig.aggregationTypes.forEach((t) => {
					this.aggregations |= t;
				});
			} else {
				this.aggregations = this.assignDefaultAggregations();
			}
		} else {
			this.aggregations = this.assignDefaultAggregations();
		}
	}
}
