import { AggregateId, DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin/origin-item-entity';

export class StructurePreparedEntitiesSetEvent extends DomainEvent {

	constructor(aggregateId: AggregateId,
				private readonly preparedItems: Array<OriginItemEntity>) {
		super(aggregateId, 'StructurePreparedEntitiesSetEvent');
	}

	getPreparedItems(): Array<OriginItemEntity> {
		return this.preparedItems;
	}
}
