import { Command } from '@generic-ui/hermes';
import { StructureId } from '../../../structure-id';
import { SchemaTheme } from '../../schema-theme';

export class SetSchemaThemeCommand extends Command {

	constructor(structureId: StructureId,
				private readonly theme: SchemaTheme) {
		super(structureId, 'SetSchemaThemeCommand');
	}

	getTheme(): SchemaTheme {
		return this.theme;
	}

}
