import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map, take } from 'rxjs/operators';

import { SchemaRepository } from '../../domain/schema/read/schema-repository';
import { StructureId } from '../../domain/structure-id';
import { globalStructureId } from '../../domain/global-structure-id';
import { Schema } from '../../domain/schema/read/schema';
import { SchemaColumnHeader } from '../../domain/schema/read/schema-column-header';
import { SchemaCssClass } from '../../domain/schema/read/schema-css-class';
import { SchemaTheme } from '../../domain/schema/schema-theme';


@Injectable()
export class SchemaReadModelService {

	constructor(private schemaRepository: SchemaRepository) {
	}

	onSchema(structureId: StructureId = globalStructureId): Observable<Schema> {
		return this.schemaRepository.on(structureId);
	}

	onSingleSchema(structureId: StructureId = globalStructureId): Observable<Schema> {
		return this.onSchema(structureId)
				   .pipe(
					   take(1)
				   );
	}

	onHeight(structureId: StructureId = globalStructureId): Observable<number> {
		return this.schemaRepository
				   .on(structureId)
				   .pipe(
					   map((schema: Schema) => {
						   return schema.getHeight();
					   }),
					   filter((height: number) => height !== undefined)
				   );
	}

	onContainerHeight(structureId: StructureId = globalStructureId): Observable<number> {
		return this.schemaRepository
				   .on(structureId)
				   .pipe(
					   map((schema: Schema) => {
						   return schema.getContainerHeight();
					   }),
					   filter((height: number) => height !== undefined)
				   );
	}

	onCssClasses(structureId: StructureId = globalStructureId): Observable<SchemaCssClass> {
		return this.schemaRepository
				   .on(structureId)
				   .pipe(
					   map((schema: Schema) => {
						   return schema.getCssClasses();
					   })
				   );
	}

	onSingleTheme(structureId: StructureId = globalStructureId): Observable<SchemaTheme> {
		return this.schemaRepository
				   .on(structureId)
				   .pipe(
					   take(1),
					   map((schema: Schema) => {
						   return schema.getTheme();
					   })
				   );
	}

	onColumnHeader(structureId: StructureId = globalStructureId): Observable<SchemaColumnHeader> {
		return this.schemaRepository
				   .on(structureId)
				   .pipe(
					   map((schema: Schema) => {
						   return schema.getColumnHeader();
					   })
				   );
	}

}
