import { AggregateEvent, AggregateId } from '@generic-ui/hermes';

import { OriginItemEntity } from './origin-item-entity';

export class StructureOriginChangedAggregateEvent extends AggregateEvent {

	constructor(aggregateId: AggregateId,
				private readonly origin: Array<OriginItemEntity>) {
		super(aggregateId, 'StructureOriginChangedAggregateEvent');
	}

	getOrigin(): Array<OriginItemEntity> {
		return this.origin;
	}
}
