import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map, switchMap, take } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureReadModelService } from '../structure/structure-read-model.service';
import { StructureId } from '../../domain/structure-id';
import { SelectedRowToggledEvent } from '../../domain/formation/command/toggle/selected-row-toggled.event';
import { Structure } from '../../domain/structure/read/structure';
import { ItemEntity } from '../../domain/source/item.entity';


@Injectable()
export class FormationEventService {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly structureQueryService: StructureReadModelService) {
	}

	onItemSelected(structureId: StructureId): Observable<Array<any>> {

		return this.domainEventBus
				   .ofEvent(
					   SelectedRowToggledEvent
				   )
				   .pipe(
					   filter((event: DomainEvent) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent) => {

						   return this.structureQueryService
									  .onStructure(structureId)
									  .pipe(
										  take(1),
										  map((str: Structure) => str.getSelectedEntities()),
										  map((entities: Array<ItemEntity>) => entities.map(ent => ent.getData()))
									  );
					   })
				   );
	}

}
