import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';

import { Paging } from '../../../../domain/paging/read/paging';
import { PagingCommandService } from '../../../../ui-api/paging/paging-command.service';


@Component({
	selector: 'gui-structure-alternative-paging-navigator',
	templateUrl: `structure-alternative-paging-navigator.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class StructureAlternativePagingNavigatorComponent implements OnChanges {

	@Input()
	paging: Paging;

	@Input()
	sourceSize: number;

	@Output()
	nextPageChanged = new EventEmitter();

	@Output()
	prevPageChanged = new EventEmitter();

	prevDisabled: boolean = false;

	nextDisabled: boolean = false;

	constructor(private pagingCommandService: PagingCommandService) {
	}

	ngOnChanges() {
		this.calculatePrev();
		this.calculateNext();
	}

	prevPage(): void {
		this.prevPageChanged.emit();
	}

	nextPage(): void {
		this.nextPageChanged.emit();
	}

	firstPage(): void {
		this.pagingCommandService.goToPage(1, this.paging.getPage());
	}

	lastPage(): void {
		let numberOfPages = Math.ceil(this.sourceSize / this.paging.getPageSize());
		this.pagingCommandService.goToPage(numberOfPages, this.paging.getPage());
	}

	private calculatePrev(): void {

		if (!this.paging) {
			return;
		}

		this.prevDisabled = this.paging.isPrevPageDisabled();
	}

	private calculateNext(): void {

		if (!this.paging && !this.sourceSize) {
			return;
		}

		this.nextDisabled = this.paging.isNextPageDisabled();
	}

}
