import {
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	Input,
	OnInit,
	PLATFORM_ID,
	Renderer2,
	ViewEncapsulation
} from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { combineLatest } from 'rxjs';

import { SmartComponent } from '../../../../common/cdk/smart-component';

import { CellTemplateWithAccessor } from '../../../composition/domain/read/definition/cell-template-with-accessor';
import { FormationCommandService } from '../../ui-api/formation/formation-command.service';
import { StructureCellEditArchive } from '../edit/structure.cell-edit.archive';
import { StructureEditModeArchive } from '../edit/structure.edit-mode.archive';
import { FormationReadModelService } from '../../ui-api/formation/formation-read-model.service';
import { SelectedRowsList } from '../../domain/formation/read/selected-rows/selected-rows-list';
import { ItemEntity } from '../../domain/source/item.entity';
import { VerticalFormationRepository } from '../../domain/structure/read/vertical-formation/vertical-formation.repository';
import { StructureReadModelService } from '../../ui-api/structure/structure-read-model.service';
import { StructureRowSelectEnabledArchive } from './row/structure.row-select-enabled.archive';
import { StructureId } from '../../domain/structure-id';
import { StructureSearchPhraseRepository } from '../../domain/structure/read/searching/phrase/structure.search-phrase.repository';
import { StructureSearchHighlightArchive } from '../../domain/structure/read/searching/highlight/structure.search-highlight.archive';


@Component({
	selector: 'gui-structure-content',
	templateUrl: `structure-content.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContentComponent extends SmartComponent implements OnInit {

	@Input()
	source: Array<ItemEntity>;

	@Input()
	columns: Array<CellTemplateWithAccessor>;

	editMode = false;

	cellEditing = false;

	rowHeight: number;

	searchPhrase: string = '';

	highlighting: boolean = true;

	phrase: boolean = true;

	rowSelecting = StructureRowSelectEnabledArchive.DEFAULT_ROW_SELECT;

	rowDetailOpened = 4;

	constructor(@Inject(PLATFORM_ID) private platformId: any,
				private renderer: Renderer2,
				private elementRef: ElementRef,
				private changeDetectorRef: ChangeDetectorRef,
				private formationCommandService: FormationCommandService,
				private structureCellEditArchive: StructureCellEditArchive,
				private structureEditModeArchive: StructureEditModeArchive,
				private formationQueryService: FormationReadModelService,
				private structureReadModelService: StructureReadModelService,
				private structureRowSelectEnabledArchive: StructureRowSelectEnabledArchive,
				private verticalFormationRepository: VerticalFormationRepository,
				private structureId: StructureId, // REfactor
				private structureSearchPhraseRepository: StructureSearchPhraseRepository,
				private structureSearchHighlightArchive: StructureSearchHighlightArchive) {
		super();
	}

	ngOnInit() {

		this.formationQueryService
			.onSelectedRows()
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowsList: SelectedRowsList) => {

				this.clearSelectedRows();

				rowsList.getIds().forEach((id: string) => {

					const strippedId = 'gui' + id.replace(/-/g, ''),
						selectedRowEl = this.elementRef.nativeElement.querySelector(`#${strippedId}`);

					if (selectedRowEl) {
						this.renderer.addClass(selectedRowEl, 'selected');
					}
				});
			});

		this.structureEditModeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.editMode = enabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureCellEditArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				this.cellEditing = enabled;
				this.changeDetectorRef.detectChanges();
			});

		this.structureReadModelService
			.onRowHeight()
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowHeight: number) => {
				this.rowHeight = rowHeight;
				this.changeDetectorRef.detectChanges();
			});

		this.structureRowSelectEnabledArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((rowSelecting: boolean) => {
				this.rowSelecting = rowSelecting;
			});

		combineLatest(
			this.structureSearchPhraseRepository
				.onPhrase(this.structureId),
			this.structureSearchHighlightArchive
				.onValue()
		).pipe(
			this.takeUntil()
		 )
		 .subscribe((args: Array<any>) => {

			 const searchPhrase = args[0],
				 highlighting = args[1];

			 this.highlighting = highlighting;
			 this.searchPhrase = searchPhrase;

			 if (!this.highlighting) {
				 this.searchPhrase = '';
			 }

			 this.changeDetectorRef.detectChanges();
		 });
	}

	trackByFn() {
		return 0;
	}

	translateY(index: number): string {

		let height = index * this.rowHeight;

		// if (index > this.rowDetailOpened) {
		// 	height += 200;
		// }

		return `translateY(${height}px)`;
	}

	toggleSelectedRow(entity: ItemEntity): void {
		if (this.rowSelecting) {
			this.formationCommandService.toggleSelectedRow((entity as any).id);
		}
	}

	private clearSelectedRows(): void {
		const selectedElements = this.elementRef.nativeElement.querySelectorAll('.selected');

		if (isPlatformBrowser(this.platformId)) {
			selectedElements.forEach((el: any) => {
				this.renderer.removeClass(el, 'selected');
			});
		}

	}

}
