import { AggregateEvent, AggregateId } from '@generic-ui/hermes';
import { AggregatedValues } from './calculation/aggregated.values';


export class StructureAggregationsChangedAggregateEvent extends AggregateEvent {

	constructor(aggregateId: AggregateId,
				private readonly aggregatedValues: Map<string, AggregatedValues>) {
		super(aggregateId, 'StructureAggregationsChangedAggregateEvent');
	}

	getAggregations(): Map<string, AggregatedValues> {
		return this.aggregatedValues;
	}
}
