import { SchemaRowColoring } from '../schema-row-coloring';
import { SchemaTheme } from '../schema-theme';
import { SchemaColumnHeader } from './schema-column-header';
import { SchemaCssClass } from './schema-css-class';

export class Schema {

	constructor(private readonly width: number,
				private readonly height: number,
				private readonly containerHeight: number,
				private readonly verticalGridEnabled: boolean,
				private readonly horizontalGridEnabled: boolean,
				private readonly theme: SchemaTheme,
				private readonly columnHeader: SchemaColumnHeader,
				private readonly rowColoring: SchemaRowColoring) {
	}

	getRowColoring(): SchemaRowColoring {
		return this.rowColoring;
	}

	getTheme(): SchemaTheme {
		return this.theme;
	}

	getHeight(): number {
		return this.height;
	}

	getContainerHeight(): number {
		return this.containerHeight;
	}

	getWidth(): number {
		return this.width;
	}

	isVerticalGridEnabled(): boolean {
		return this.verticalGridEnabled;
	}

	isHorizontalGridEnabled(): boolean {
		return this.horizontalGridEnabled;
	}

	getColumnHeader(): SchemaColumnHeader {
		return this.columnHeader;
	}

	getCssClasses(): SchemaCssClass {
		return new SchemaCssClass(
			this.isVerticalGridEnabled(),
			this.isHorizontalGridEnabled(),
			this.getTheme(),
			this.getRowColoring()
		);
	}

}
