import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';

import { SmartComponent } from '../../../../common/cdk/smart-component';

import { Paging } from '../../domain/paging/read/paging';
import { PagingReadModelService } from '../../ui-api/paging/paging-read-model.service';
import { PagingCommandService } from '../../ui-api/paging/paging-command.service';
import { SourceReadModelService } from '../../ui-api/source/source-read-model.service';
import { StructurePagingDisplayModeArchive } from './mode/structure-paging.display-mode.archive';
import { PagingDisplayMode } from './mode/paging-display-mode';


@Component({
	selector: 'gui-structure-paging',
	templateUrl: 'structure-paging.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class StructurePagingComponent extends SmartComponent implements OnInit {

	sourceSize: number;

	paging: Paging;

	alternativeDisplay: boolean = false;

	constructor(private changeDetectorRef: ChangeDetectorRef,
				private pagingQueryService: PagingReadModelService,
				private pagingCommandService: PagingCommandService,
				private sourceQueryService: SourceReadModelService,
				private structurePagingDisplayModeArchive: StructurePagingDisplayModeArchive) {
		super();
	}

	ngOnInit() {

		this.structurePagingDisplayModeArchive
			.onValue()
			.pipe(
				this.takeUntil()
			)
			.subscribe((mode: PagingDisplayMode) => {
				this.alternativeDisplay = mode === PagingDisplayMode.ADVANCED;
				this.changeDetectorRef.detectChanges();
			});

		this.pagingQueryService
			.onPaging()
			.pipe(
				this.takeUntil()
			)
			.subscribe((paging: Paging) => {
				this.paging = paging;
				this.changeDetectorRef.detectChanges();
			});

		this.sourceQueryService
			.onOriginSize()
			.pipe(this.takeUntil())
			.subscribe((size: number) => {
				this.sourceSize = size;
				this.changeDetectorRef.detectChanges();
			});
	}

	changePageSize(pageSize: number) {
		this.pagingCommandService.changePageSize(pageSize);
	}

	nextPage(): void {

		if (!this.sourceSize) {
			return;
		}

		this.pagingCommandService.nextPage();
	}

	prevPage(): void {
		this.pagingCommandService.prevPage();
	}

}
