import { Injectable } from '@angular/core';

import { FormationConverter } from '../formation/formation.converter';
import { SchemaConverter } from '../schema/schema.converter';
import { PagingConverter } from '../paging/paging.converter';
import { SourceConverter } from '../source/source.converter';
import { VerticalFormationConverter } from './read/vertical-formation/vertical-formation.converter';
import { StructureAggregate } from './command/structure.aggregate';
import { Structure } from './read/structure';
import { OriginItemEntity } from '../source/command/origin/origin-item-entity';
import { ItemEntity } from '../source/item.entity';


@Injectable()
export class StructureConverter {

	constructor(private formationConverter: FormationConverter,
				private schemaConverter: SchemaConverter,
				private pagingConverter: PagingConverter,
				private sourceConverter: SourceConverter,
				private verticalFormationConverter: VerticalFormationConverter) {
	}

	convert(aggregate: StructureAggregate): Structure {

		const formationAggregate = aggregate.getFormation(),
			schemaAggregate = aggregate.getSchema(),
			pagingAggregate = aggregate.getPaging(),
			sourceQuery = aggregate.getEntities(),
			sourceCoordinator = aggregate.getSource(),
			verticalFormation = aggregate.getVerticalFormation();

		return new Structure(
			aggregate.getId(),
			this.formationConverter.convert(formationAggregate),
			this.schemaConverter.convert(schemaAggregate),
			this.pagingConverter.convert(pagingAggregate),
			this.convertSource(sourceQuery),
			this.sourceConverter.convert(sourceCoordinator),
			this.verticalFormationConverter.convert(verticalFormation)
		);
	}

	private convertSource(source: Array<OriginItemEntity>): Array<ItemEntity> {
		return source.map((originItem: OriginItemEntity) => {
			return new ItemEntity(originItem.rawData, originItem.getPosition(), originItem.id, originItem.getVersion());
		});
	}
}
