import { Injectable } from '@angular/core';

import { Archive } from '../../../../../../common/cdk/archive';

import { StructureColumnMenuConfig } from './structure.column-menu-config';
import { ColumnMenuConfig } from '../../../../ui-api/structure/column-menu-config';

@Injectable()
export class StructureColumnMenuConfigArchive extends Archive<StructureColumnMenuConfig> {

	constructor() {
		super(StructureColumnMenuConfig.default());
	}

	nextConfig(config: ColumnMenuConfig): void {

		const menuConfig = StructureColumnMenuConfig.fromConfig(config);

		this.next(menuConfig);
	}

}
