import { Injectable } from '@angular/core';
import { SchemaAggregate } from './command/schema-aggregate';
import { Schema } from './read/schema';
import { ColumnHeaderEntity } from './command/column-header/column-header.entity';
import { SchemaColumnHeader } from './read/schema-column-header';


@Injectable()
export class SchemaConverter {

	convert(aggregate: SchemaAggregate): Schema {

		const columnHeader = aggregate.getColumnHeader();

		return new Schema(
			aggregate.getWidth(),
			aggregate.getHeight(),
			aggregate.getContainerHeight(),
			aggregate.isVerticalGridEnabled(),
			aggregate.isHorizontalGridEnabled(),
			aggregate.getTheme(),
			this.convertColumnHeader(columnHeader),
			aggregate.getRowColoring()
		);
	}

	private convertColumnHeader(columnHeader: ColumnHeaderEntity): SchemaColumnHeader {
		return new SchemaColumnHeader(columnHeader.isHeaderOnTop(), columnHeader.isHeaderOnBottom());
	}

}
