export class ColumnHeaderEntity {

	constructor(private top: boolean,
				private bottom: boolean) {
	}

	setHeaderTop(enabled: boolean): void {
		this.top = enabled;
	}

	setHeaderBottom(enabled: boolean): void {
		this.bottom = enabled;
	}

	isHeaderOnTop(): boolean {
		return this.top;
	}

	isHeaderOnBottom(): boolean {
		return this.bottom;
	}

}
