import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { Override } from '../../../../../common/cdk/decorators';

import { CompositionReadModelService } from '../../../../composition/ui-api/composition-read-model.service';
import { CompositionId } from '../../../../composition/domain/composition-id';
import { CompositionRepository } from '../../../../composition/domain/read/composition.repository';
import { CellTemplateWithContext } from '../../../../composition/domain/read/definition/cell-template-with-context';
import { CellTemplateWithAccessor } from '../../../../composition/domain/read/definition/cell-template-with-accessor';
import { globalCompositionId } from '../../../../composition/domain/global-composition-id';
import { FieldId } from '../../../domain/structure/command/field/data-type/field.id';
import { SortOrder } from '../../../../composition/domain/command/column/sort/sort-order';


@Injectable()
export class LocalCompositionReadModelService extends CompositionReadModelService {

	constructor(private readonly compositionId: CompositionId,
				compositionRepository: CompositionRepository) {
		super(compositionRepository);
	}

	@Override
	onWidth(): Observable<number> {
		return super.onWidth(this.compositionId);
	}

	@Override
	onContainerWidth(compositionId: CompositionId = globalCompositionId): Observable<number> {
		return super.onContainerWidth(this.compositionId);
	}

	@Override
	onWidthForEachColumn(): Observable<Array<number>> {
		return super.onWidthForEachColumn(this.compositionId);
	}

	@Override
	onHeaderColumns(): Observable<Array<CellTemplateWithContext>> {
		return super.onHeaderColumns(this.compositionId);
	}

	@Override
	onAllColumns(compositionId: CompositionId = globalCompositionId): Observable<Array<CellTemplateWithContext>> {
		return super.onAllColumns(this.compositionId);
	}

	@Override
	onSortOrder(fieldId: FieldId): Observable<SortOrder> {
		return super.onSortOrder(fieldId, this.compositionId);
	}

	@Override
	onTemplateColumns(): Observable<Array<CellTemplateWithAccessor>> {
		return super.onTemplateColumns(this.compositionId);
	}

	@Override
	onResizeWidth(): Observable<boolean> {
		return super.onResizeWidth(this.compositionId);
	}

}
