import { ValueObject } from '@generic-ui/hermes';

import { ColumnPresentation } from '../column.presentation';
import { ColumnAlign } from '../../../../column-align';
import { CellView } from '../../../../cell-view';


@ValueObject
export class BooleanColumnPresentation extends ColumnPresentation {

	static getInstance(): BooleanColumnPresentation {
		if (!BooleanColumnPresentation.instance) {
			BooleanColumnPresentation.instance = new BooleanColumnPresentation();
		}
		return BooleanColumnPresentation.instance;
	}

	private static instance: BooleanColumnPresentation = null;

	private constructor() {
		super();
	}

	getPossibleViews(): Array<CellView> {
		return [
			CellView.TEXT,
			CellView.ITALIC,
			CellView.CHIP,
			CellView.BOLD,
			CellView.CHECKBOX
		];
	}

	getDefaultView(): CellView {
		return CellView.CHECKBOX;
	}

	getDefaultAlign(): ColumnAlign {
		return ColumnAlign.CENTER;
	}
}
