import { AggregateEvent, AggregateId } from '@generic-ui/hermes';
import { ActiveColumnEntity } from '../active/active-column.entity';

export class CompositionColumnSetEnabledAggregateEvent extends AggregateEvent {

	constructor(aggregateId: AggregateId,
				private readonly activeColumns: Array<ActiveColumnEntity>) {
		super(aggregateId, 'CompositionColumnSetEnabledAggregateEvent');
	}

	getActiveColumns(): Array<ActiveColumnEntity> {
		return this.activeColumns;
	}

}
