import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { distinctUntilChanged, map, take } from 'rxjs/operators';

import { StructureRepository } from '../../domain/structure/read/structure.repository';
import { StructureSourceOriginRepository } from '../../domain/source/read/origin/structure.source-origin.repository';
import { StructureId } from '../../domain/structure-id';
import { globalStructureId } from '../../domain/global-structure-id';
import { Structure } from '../../domain/structure/read/structure';
import { ItemEntity } from '../../domain/source/item.entity';
import { StructurePreparedItemsRepository } from '../../domain/source/read/prepared/structure.prepared-items.repository';


@Injectable()
export class SourceReadModelService {

	constructor(private structureRepository: StructureRepository,
				private structurePreparedItemsRepository: StructurePreparedItemsRepository,
				private structureSourceOriginRepository: StructureSourceOriginRepository) {
	}

	onEntities(structureId: StructureId = globalStructureId): Observable<Array<ItemEntity>> {

		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   map((structure: Structure) => structure.getEntities()),
					   distinctUntilChanged((ents1: Array<ItemEntity>, ents2: Array<ItemEntity>) => {
						   if (ents1.length !== ents2.length) {
							   return false;
						   }

						   let flag = true;

						   ents1.forEach((ent, index) => {
							   if (!ent.equals(ents2[index])) {
								   flag = false;
								   return;
							   }
						   });

						   return flag;
					   })
				   );
	}

	onEntitiesSize(structureId: StructureId = globalStructureId): Observable<number> {

		return this.onEntities(structureId)
				   .pipe(
					   map((entities: Array<ItemEntity>) => {
						   return entities.length;
					   })
				   );
	}

	onSingleEntities(structureId: StructureId = globalStructureId): Observable<Array<ItemEntity>> {

		return this.onEntities(structureId)
				   .pipe(
					   take(1)
				   );
	}

	onOriginSize(structureId: StructureId = globalStructureId): Observable<number> {
		return this.structureSourceOriginRepository
				   .onOrigin(structureId)
				   .pipe(
					   map(origin => origin.length)
				   );
	}

	onLoading(structureId: StructureId = globalStructureId): Observable<boolean> {
		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   map((structure: Structure) => structure.getSource().isLoading())
				   );
	}

	onPreparedEntities(structureId: StructureId = globalStructureId): Observable<any> {
		return this.structurePreparedItemsRepository.onPreparedItems(structureId);
	}

}
