import { Injectable, Type } from '@angular/core';

import { Command, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSchemaVerticalGridCommand } from './set-schema-vertical-grid.command';
import { SchemaVerticalGridSetEvent } from './schema-vertical-grid-set.event';
import { StructureAggregate } from '../../../../structure/command/structure.aggregate';


@Injectable()
export class SetSchemaVerticalGridCommandHandler implements CommandHandler<StructureAggregate, SetSchemaVerticalGridCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetSchemaVerticalGridCommand> {
		return SetSchemaVerticalGridCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: Command): void {
		this.domainEventPublisher.publish(new SchemaVerticalGridSetEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetSchemaVerticalGridCommand): void {

		const enabled = command.isEnabled(),
			schema = aggregate.getSchema();

		schema.setVerticalGrid(enabled);
	}

}
