import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../../domain/structure-id';
import { StructureAggregationsChangedEvent } from '../../../domain/structure/command/aggregation/structure.aggregations-changed.event';


@Injectable()
export class StructureAggregationUiEventsRepository {

	constructor(private readonly domainEventBus: DomainEventBus) {
	}

	onAggregationsChanged(structureId: StructureId): Observable<DomainEvent> {

		return this.domainEventBus
				   .pipe(
					   filter((event: DomainEvent) => event instanceof StructureAggregationsChangedEvent),
					   filter((event: DomainEvent) => event.getAggregateId().toString() === structureId.toString())
				   );
	}
}
