import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { SchemaReadModelService } from '../../../ui-api/schema/schema-read-model.service';
import { StructureId } from '../../../domain/structure-id';
import { SchemaRepository } from '../../../domain/schema/read/schema-repository';
import { Override } from '../../../../../common/cdk/decorators';
import { Schema } from '../../../domain/schema/read/schema';
import { SchemaCssClass } from '../../../domain/schema/read/schema-css-class';
import { globalStructureId } from '../../../domain/global-structure-id';
import { SchemaColumnHeader } from '../../../domain/schema/read/schema-column-header';
import { SchemaTheme } from '../../../domain/schema/schema-theme';


@Injectable()
export class LocalSchemaReadModelService extends SchemaReadModelService {

	constructor(private structureId: StructureId,
				schemaRepository: SchemaRepository) {
		super(schemaRepository);
	}

	@Override
	onSchema(): Observable<Schema> {
		return super.onSchema(this.structureId);
	}

	@Override
	onSingleSchema(): Observable<Schema> {
		return super.onSingleSchema(this.structureId);
	}

	@Override
	onHeight(): Observable<number> {
		return super.onHeight(this.structureId);
	}

	@Override
	onContainerHeight(): Observable<number> {
		return super.onContainerHeight(this.structureId);
	}

	@Override
	onCssClasses(): Observable<SchemaCssClass> {
		return super.onCssClasses(this.structureId);
	}

	@Override
	onSingleTheme(): Observable<SchemaTheme>  {
		return super.onSingleTheme(this.structureId);
	}

	@Override
	onColumnHeader(structureId: StructureId = globalStructureId): Observable<SchemaColumnHeader> {
		return super.onColumnHeader(this.structureId);
	}

}
