import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureAggregateRepository } from '../../../../structure/command/structure-aggregate.repository';
import { ChangeSchemaTopHeaderCommand } from './change-schema-top-header.command';
import { SchemaTopHeaderChangedEvent } from './schema-top-header-changed.event';
import { StructureAggregate } from '../../../../structure/command/structure.aggregate';


@Injectable()
export class ChangeSchemaTopHeaderCommandHandler implements CommandHandler<StructureAggregate, ChangeSchemaTopHeaderCommand> {

	constructor(private structureAggregateRepository: StructureAggregateRepository,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<ChangeSchemaTopHeaderCommand> {
		return ChangeSchemaTopHeaderCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: ChangeSchemaTopHeaderCommand): void {
		this.domainEventPublisher.publish(new SchemaTopHeaderChangedEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: ChangeSchemaTopHeaderCommand): void {

		const schema = aggregate.getSchema(),
			enabled = command.isEnabled();

		schema.setHeaderTop(enabled);
	}

}
