import { Injectable, Type } from '@angular/core';

import { Command, CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetRowColoringCommand } from './set-row-coloring.command';
import { RowColoringSetEvent } from './row-coloring-set.event';
import { StructureAggregate } from '../../../../structure/command/structure.aggregate';


@Injectable()
export class SetRowColoringCommandHandler implements CommandHandler<StructureAggregate, SetRowColoringCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetRowColoringCommand> {
		return SetRowColoringCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: Command): void {
		this.domainEventPublisher.publish(new RowColoringSetEvent(command.getAggregateId()));
	}

	handleAggregate(structure: StructureAggregate, command: SetRowColoringCommand): void {

		// missing implementation
		const schema = structure.getSchema(),
			coloring = command.getColoring();

		schema.setRowColoring(coloring);
	}

}
