import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map, switchMap, take } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { PagingReadModelService } from './paging-read-model.service';
import { StructureId } from '../../domain/structure-id';
import { NextPageEvent } from '../../domain/paging/command/next-page/next-page.event';
import { PrevPageEvent } from '../../domain/paging/command/prev-page/prev-page.event';
import { PageChangedEvent } from '../../domain/paging/command/page/page-changed.event';
import { Paging } from '../../domain/paging/read/paging';
import { PagesizeChangedEvent } from '../../domain/paging/command/change-pagesize/pagesize-changed.event';


@Injectable()
export class PagingEventService {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly pagingQueryService: PagingReadModelService) {
	}

	onPageChange(structureId: StructureId): Observable<number> {

		return this.domainEventBus
				   .ofEvent(
					   NextPageEvent,
					   PrevPageEvent,
					   PageChangedEvent
				   )
				   .pipe(
					   filter((event: DomainEvent) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent) => {

						   return this.pagingQueryService
									  .onPaging(structureId)
									  .pipe(
										  map((paging: Paging) => paging.getPage()),
										  take(1)
									  );
					   })
				   );
	}

	onPageSizeChange(structureId: StructureId): Observable<number> {

		return this.domainEventBus
				   .ofEvent(
					   PagesizeChangedEvent
				   )
				   .pipe(
					   filter((event: DomainEvent) => event.getAggregateId().toString() === structureId.toString()),
					   switchMap((event: DomainEvent) => {

						   return this.pagingQueryService
									  .onPaging(structureId)
									  .pipe(
										  map((paging: Paging) => paging.getPageSize()),
										  take(1)
									  );
					   })
				   );
	}

}
