import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	OnChanges,
	OnInit,
	Renderer2,
	SimpleChanges,
	ViewEncapsulation
} from '@angular/core';
import { timer } from 'rxjs';

import { StructureDefinition } from './structure-definition';
import { StructureIdGenerator } from './structure-id.generator';
import { StructureId } from '../domain/structure-id';
import { CompositionId } from '../../composition/domain/composition-id';
import { localProviders } from './local/local-providers';
import { SchemaCssClassManager } from './schema/schema-css-class.manager';
import { StructureCellEditArchive } from './edit/structure.cell-edit.archive';
import { StructureCellEditStore } from './edit/structure.cell-edit.store';
import { StructureEditModeArchive } from './edit/structure.edit-mode.archive';
import { StructureInfoPanelEnabledArchive } from './panel/info/structure.info-panel-enabled.archive';
import { StructureAggregationArchive } from './panel/aggregation/structure.aggregation.archive';
import { StructureAggregationConfigService } from './panel/aggregation/structure.aggregation-config.service';
import { structureComponentToken } from './structure-component-token';
import { StructureGateway } from './gateway/structure.gateway';
import { SchemaColumnHeader } from '../domain/schema/read/schema-column-header';
import { Structure } from '../domain/structure/read/structure';
import { PagingCommandService } from '../ui-api/paging/paging-command.service';
import { PagingEventService } from '../ui-api/paging/paging-event.service';
import { SourceCommandService } from '../ui-api/source/source-command.service';
import { SourceEventService } from '../ui-api/source/event/source-event.service';
import { SchemaCommandService } from '../ui-api/schema/schema-command.service';
import { CompositionCommandService } from '../../composition/ui-api/composition.command-service';
import { CompositionEventService } from '../../composition/ui-api/composition.event-service';
import { FormationEventService } from '../ui-api/formation/formation-event.service';
import { StructureCommandService } from '../ui-api/structure/structure-command.service';
import { StructureReadModelService } from '../ui-api/structure/structure-read-model.service';
import { CompositionReadModelService } from '../../composition/ui-api/composition-read-model.service';
import { SchemaReadModelService } from '../ui-api/schema/schema-read-model.service';
import { StructureColumnMenuConfigArchive } from './header/menu/config/structure.column-menu-config.archive';
import { StructurePagingDisplayModeArchive } from './paging/mode/structure-paging.display-mode.archive';
import { StructureRowSelectEnabledArchive } from './content/row/structure.row-select-enabled.archive';
import { StructureSearchEventService } from '../ui-api/structure/search/structure-search-event.service';
import { StructureCellEditCloseAllService } from './edit/structure.cell-edit-close-all.service';

/** @internal */
export function structureIdFactory(generator: StructureIdGenerator): StructureId {
	return new StructureId('gui-' + generator.generateId());
}

/** @internal */
export function compositionIdFactory(generator: StructureIdGenerator): CompositionId {
	return new CompositionId('gui-' + generator.generateId());
}

/** @internal */
export const structureComponentSelfProviders = [
	{
		provide: StructureId,
		useFactory: structureIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	{
		provide: CompositionId,
		useFactory: compositionIdFactory,
		deps: [
			StructureIdGenerator
		]
	},
	...localProviders,

	SchemaCssClassManager,
	StructureCellEditArchive,
	StructureCellEditCloseAllService,
	StructureCellEditStore,
	StructureEditModeArchive,
	StructureInfoPanelEnabledArchive,
	StructureAggregationArchive,
	StructureAggregationConfigService,
	StructureColumnMenuConfigArchive,
	StructurePagingDisplayModeArchive,
	StructureRowSelectEnabledArchive
];

/** @internal */
@Component({
	selector: 'gui-structure',
	templateUrl: './structure.component.html',
	styleUrls: [
		'./style/styles.ngx.scss',
		'./style/structure.ngx.scss',
		'./style/structure-column-manager.ngx.scss',
		'./style/structure-aggregation.ngx.scss',
		'./style/structure-info-modal.ngx.scss',
		'./style/structure-paging.ngx.scss',
		'./style/structure-responsive.ngx.scss',
		'./style/structure-flex.ngx.scss',
		'../../composition/ui/composition.ngx.scss',
		'./style/themes/fabric.structure.ngx.scss',
		'./style/themes/material.structure.ngx.scss',
		'./style/themes/dark.structure.ngx.scss',
		'./style/themes/light.structure.ngx.scss',
		'./style/themes/generic.structure.ngx.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	host: {
		'[class]': `"gui-structure"`,
		// '[style.min-height.px]': 'structureHeight',
		'[class.gui-structure-border]': 'isBorderEnabled()',
		'[id]': 'structureId.toString()'
	},
	providers: [
		...structureComponentSelfProviders,
		{
			provide: structureComponentToken,
			useExisting: StructureComponent
		}
	]
})
export class StructureComponent extends StructureGateway implements OnChanges, OnInit, AfterViewInit {

	loaderEnabled: boolean = false;

	circleLoaderEnabled: boolean = true;

	initialLoaderAnimation: boolean = false;

	structureHeight: number;

	private columnHeader: SchemaColumnHeader;

	private structure: Structure;

	constructor(structureId: StructureId,
				compositionId: CompositionId,
				pagingCommandService: PagingCommandService,
				pagingEventService: PagingEventService,
				sourceCommandService: SourceCommandService,
				sourceEventService: SourceEventService,
				schemaCommandService: SchemaCommandService,
				compositionCommandService: CompositionCommandService,
				compositionEventService: CompositionEventService,
				formationEventService: FormationEventService,
				structureCommandService: StructureCommandService,
				structureEditModeArchive: StructureEditModeArchive,
				structureCellEditArchive: StructureCellEditArchive,
				structureInfoPanelEnabledArchive: StructureInfoPanelEnabledArchive,
				structureAggregationConfigService: StructureAggregationConfigService,
				structureCellEditStore: StructureCellEditStore,
				structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				structurePagingDisplayModeArchive: StructurePagingDisplayModeArchive,
				structureRowSelectEnabledArchive: StructureRowSelectEnabledArchive,
				structureSearchEventService: StructureSearchEventService,
				private elementRef: ElementRef,
				private changeDetectorRef: ChangeDetectorRef,
				private renderer: Renderer2,
				private structureDefinition: StructureDefinition,
				private structureQueryService: StructureReadModelService,
				private compositionQueryService: CompositionReadModelService,
				private schemaReadModelService: SchemaReadModelService,
				private schemaStylesManager: SchemaCssClassManager) {

		super(
			structureId,
			compositionId,
			structureCommandService,
			pagingCommandService,
			pagingEventService,
			sourceCommandService,
			sourceEventService,
			schemaCommandService,
			compositionCommandService,
			compositionEventService,
			formationEventService,
			structureEditModeArchive,
			structureCellEditArchive,
			structureInfoPanelEnabledArchive,
			structureAggregationConfigService,
			structureCellEditStore,
			structureColumnMenuConfigArchive,
			structurePagingDisplayModeArchive,
			structureRowSelectEnabledArchive,
			structureSearchEventService
		);

		this.structureCommandService.createStructure();
		compositionCommandService.createComposition();
	}

	ngOnChanges(changes: SimpleChanges) {
		super.ngOnChanges(changes);
	}

	ngOnInit() {
		super.ngOnInit();

		this.schemaReadModelService
			.onHeight()
			.pipe(this.takeUntil())
			.subscribe((height: number) => {
				this.structureHeight = height;
			});

		this.schemaReadModelService
			.onColumnHeader()
			.pipe(this.takeUntil())
			.subscribe((columnHeader: SchemaColumnHeader) => {
				this.columnHeader = columnHeader;
			});

		this.structureQueryService
			.onStructure()
			.pipe(this.takeUntil())
			.subscribe((structure: Structure) => {
				this.structure = structure;

				this.loaderEnabled = structure.getSource().isLoading();

				this.circleLoaderEnabled = structure.isLoaderVisible();

				if (this.loaderEnabled && !this.initialLoaderAnimation) {
					this.initialLoaderAnimation = true;
				}
			});
	}

	ngAfterViewInit() {
		let width = this.elementRef.nativeElement.offsetWidth;

		if (width > 0) {
			this.compositionCommandService.setContainerWidth(width);
		} else {

			/**
			 * When gui-grid is in dynamic container which is created later then grid.
			 */
			timer(0)
				.pipe(
					this.takeUntil()
				)
				.subscribe(() => {

					width = this.elementRef.nativeElement.offsetWidth;

					if (width > 0) {
						this.compositionCommandService.setContainerWidth(width);
					}
				});
		}

		this.schemaStylesManager
			.init(this.elementRef, this.structureId);

		this.compositionQueryService
			.onWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.renderer.setStyle(this.elementRef.nativeElement, 'width', width + 'px');
			});
	}

	isBorderEnabled(): boolean {
		return this.structureDefinition.isBorderEnabled();
	}

	getStructureId(): StructureId {
		return this.structureId;
	}

}
