import { ValueObject } from '@generic-ui/hermes';

import { ColumnPresentation } from '../column.presentation';
import { ColumnAlign } from '../../../../column-align';
import { CellView } from '../../../../cell-view';


@ValueObject
export class UnknownColumnPresentation extends ColumnPresentation {

	static getInstance(): UnknownColumnPresentation {
		if (!UnknownColumnPresentation.instance) {
			UnknownColumnPresentation.instance = new UnknownColumnPresentation();
		}
		return UnknownColumnPresentation.instance;
	}

	private static instance: UnknownColumnPresentation = null;

	private constructor() {
		super();
	}

	getPossibleViews(): Array<CellView> {
		return [
			CellView.TEXT
		];
	}

	getDefaultView(): CellView {
		return CellView.TEXT;
	}

	getDefaultAlign(): ColumnAlign {
		return ColumnAlign.LEFT;
	}
}
