import { ValueObject } from '@generic-ui/hermes';

import { ColumnPresentation } from '../column.presentation';
import { ColumnAlign } from '../../../../column-align';
import { CellView } from '../../../../cell-view';


@ValueObject
export class NumberColumnPresentation extends ColumnPresentation {

	static getInstance(): NumberColumnPresentation {
		if (!NumberColumnPresentation.instance) {
			NumberColumnPresentation.instance = new NumberColumnPresentation();
		}
		return NumberColumnPresentation.instance;
	}

	private static instance: NumberColumnPresentation = null;

	private constructor() {
		super();
	}

	getPossibleViews(): Array<CellView> {
		return [
			CellView.TEXT,
			CellView.BAR,
			CellView.PERCENTAGE_BAR,
			CellView.PERCENTAGE
		];
	}

	getDefaultView(): CellView {
		return CellView.NUMBER;
	}

	getDefaultAlign(): ColumnAlign {
		return ColumnAlign.RIGHT;
	}
}
