import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { ChangeSchemaBottomHeaderCommand } from './change-schema-bottom-header.command';
import { SchemaBottomHeaderChangedEvent } from './schema-bottom-header-changed.event';
import { StructureAggregate } from '../../../../structure/command/structure.aggregate';


@Injectable()
export class ChangeSchemaBottomHeaderCommandHandler implements CommandHandler<StructureAggregate, ChangeSchemaBottomHeaderCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<ChangeSchemaBottomHeaderCommand> {
		return ChangeSchemaBottomHeaderCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: ChangeSchemaBottomHeaderCommand): void {
		this.domainEventPublisher.publish(new SchemaBottomHeaderChangedEvent(command.getAggregateId()));
	}

	handleAggregate(structure: StructureAggregate, command: ChangeSchemaBottomHeaderCommand): void {

		const schema = structure.getSchema(),
			enabled = command.isEnabled();

		schema.setHeaderBottom(enabled);
	}

}
