import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { CompositionRepository } from '../domain/read/composition.repository';
import { CompositionId } from '../domain/composition-id';
import { globalCompositionId } from '../domain/global-composition-id';
import { Composition } from '../domain/read/composition';
import { CellTemplateWithContext } from '../domain/read/definition/cell-template-with-context';
import { CellTemplateWithAccessor } from '../domain/read/definition/cell-template-with-accessor';
import { SortOrder } from '../domain/command/column/sort/sort-order';
import { FieldId } from '../../structure/domain/structure/command/field/data-type/field.id';


@Injectable()
export class CompositionReadModelService {

	constructor(private compositionRepository: CompositionRepository) {
	}

	onWidth(compositionId: CompositionId = globalCompositionId): Observable<number> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getWidth())
				   );
	}

	onContainerWidth(compositionId: CompositionId = globalCompositionId): Observable<number> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getContainerWidth())
				   );
	}

	onWidthForEachColumn(compositionId: CompositionId = globalCompositionId): Observable<Array<number>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getAllColumns().map(c => c.width))
				   );
	}

	onHeaderColumns(compositionId: CompositionId = globalCompositionId): Observable<Array<CellTemplateWithContext>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getActiveHeaderColumns())
				   );
	}

	onAllColumns(compositionId: CompositionId = globalCompositionId): Observable<Array<CellTemplateWithContext>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getHeaderColumns())
				   );
	}

	onSortOrder(fieldId: FieldId, compositionId: CompositionId = globalCompositionId): Observable<SortOrder> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getActiveHeaderColumns()),
					   map((columns: Array<CellTemplateWithContext>) => {
						   return columns.filter((c) => c.getFieldId().equals(fieldId));
					   }),
					   filter((columns: Array<CellTemplateWithContext>) => {
						   return columns.length > 0;
					   }),
					   map(columns => columns[0].getSortStatus())
				   );
	}

	onTemplateColumns(compositionId: CompositionId = globalCompositionId): Observable<Array<CellTemplateWithAccessor>> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.getTemplateColumns())
				   );
	}

	onResizeWidth(compositionId: CompositionId = globalCompositionId): Observable<boolean> {
		return this.compositionRepository
				   .on(compositionId)
				   .pipe(
					   map((composition: Composition) => composition.isResizeWidthEnabled())
				   );
	}

}
