import { Inject, Injectable } from '@angular/core';

import { AGGREGATION_CALCULATORS } from './calculation/aggregation.calculators.token';
import { AggregationCalculator } from './calculation/aggregation.calculator';
import { StructureId } from '../../../structure-id';
import { AggregationManager } from './aggregation.manager';


@Injectable()
export class AggregationManagerFactory {

	constructor(@Inject(AGGREGATION_CALCULATORS) private calculators: Array<AggregationCalculator<any, any>>) {
	}

	create(structureId: StructureId): AggregationManager {
		return new AggregationManager(structureId, this.calculators);
	}

}
