import { ValueObject } from '@generic-ui/hermes';

import { ColumnPresentation } from '../column.presentation';
import { ColumnAlign } from '../../../../column-align';
import { CellView } from '../../../../cell-view';


@ValueObject
export class StringColumnPresentation extends ColumnPresentation {

	static getInstance(): StringColumnPresentation {
		if (!StringColumnPresentation.instance) {
			StringColumnPresentation.instance = new StringColumnPresentation();
		}
		return StringColumnPresentation.instance;
	}

	private static instance: StringColumnPresentation = null;

	private constructor() {
		super();
	}

	getPossibleViews(): Array<CellView> {
		return [
			CellView.TEXT,
			CellView.ITALIC,
			CellView.BOLD,
			CellView.IMAGE,
			CellView.LINK,
			CellView.CHIP
		];
	}

	getDefaultView(): CellView {
		return CellView.TEXT;
	}

	getDefaultAlign(): ColumnAlign {
		return ColumnAlign.LEFT;
	}
}
