import {
	AfterViewInit,
	ChangeDetectionStrategy,
	ChangeDetectorRef,
	Component,
	ElementRef,
	Inject,
	NgZone,
	OnDestroy,
	OnInit,
	Optional,
	Renderer2,
	ViewChild,
	ViewEncapsulation
} from '@angular/core';
import { combineLatest, fromEvent, Subject } from 'rxjs';
import { distinctUntilChanged, filter, map, takeUntil } from 'rxjs/operators';

import { ResizeDetector } from '@generic-ui/fabric';

import { SmartComponent } from '../../../../common/cdk/smart-component';
import { CellTemplateWithAccessor } from '../../../composition/domain/read/definition/cell-template-with-accessor';
import { SchemaRowColoring } from '../../domain/schema/schema-row-coloring';
import { StructureId } from '../../domain/structure-id';
import { StructureCommandService } from '../../ui-api/structure/structure-command.service';
import { StructureReadModelService } from '../../ui-api/structure/structure-read-model.service';
import { SourceReadModelService } from '../../ui-api/source/source-read-model.service';
import { SchemaReadModelService } from '../../ui-api/schema/schema-read-model.service';
import { CompositionCommandService } from '../../../composition/ui-api/composition.command-service';
import { CompositionReadModelService } from '../../../composition/ui-api/composition-read-model.service';
import { FormationReadModelService } from '../../ui-api/formation/formation-read-model.service';
import { structureParentComponent } from '../structure-parent-component';
import { Structure } from '../../domain/structure/read/structure';
import { ItemEntity } from '../../domain/source/item.entity';


@Component({
	selector: 'gui-structure-container',
	templateUrl: `structure-container.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContainerComponent extends SmartComponent implements OnInit, AfterViewInit, OnDestroy {

	@ViewChild('container', { static: false })
	containerRef: ElementRef;

	columns: Array<CellTemplateWithAccessor> = [];

	source: Array<ItemEntity> = [];

	height: number;

	rowColoring: SchemaRowColoring;

	private autoResizeWidthEnabled: boolean = false;

	private scrollObservation$ = new Subject();

	constructor(private elementRef: ElementRef,
				private renderer: Renderer2,
				private changeDetectorRef: ChangeDetectorRef,
				private ngZone: NgZone,
				private structureId: StructureId,
				private structureCommandService: StructureCommandService,
				private structureQueryService: StructureReadModelService,
				private sourceQueryService: SourceReadModelService,
				private schemaQueryService: SchemaReadModelService,
				private compositionCommandService: CompositionCommandService,
				private compositionQueryService: CompositionReadModelService,
				private formationQueryService: FormationReadModelService,
				private resizeDetector: ResizeDetector,
				@Optional() @Inject(structureParentComponent) private structureParent: any) {
		super();
	}

	ngOnInit() {

		/**
		 * TODO One source of truth
		 *
		 * There should only one sub here.
		 */
		combineLatest(
			this.sourceQueryService.onEntities(),
			this.structureQueryService.onRowHeight()
		)
			.pipe(this.takeUntil())
			.subscribe((combine: Array<any>) => {

				const source: Array<ItemEntity> = combine[0],
					rowHeight = combine[1];

				this.source = source;
				this.setHeight(source.length * rowHeight);
				this.changeDetectorRef.detectChanges();
			});

		this.compositionQueryService
			.onTemplateColumns()
			.pipe(this.takeUntil())
			.subscribe((columns: Array<CellTemplateWithAccessor>) => {
				this.columns = columns;

				this.changeDetectorRef.detectChanges();
			});

		this.compositionQueryService
			.onResizeWidth()
			.pipe(this.takeUntil())
			.subscribe((enabled: boolean) => {
				this.autoResizeWidthEnabled = enabled;
			});

		combineLatest(
			this.sourceQueryService.onEntities(),
			this.schemaQueryService.onContainerHeight(),
			this.structureQueryService.onRowHeight()
		)
			.pipe(this.takeUntil())
			.subscribe((combine: Array<any>) => {

				const source: Array<ItemEntity> = combine[0],
					height: number = combine[1],
					rowHeight = combine[2];

				const sourceSizeHeight = source.length * rowHeight;

				if (sourceSizeHeight > height) {
					this.setHeight(height);
				} else {
					this.setHeight(sourceSizeHeight);
				}

				this.changeDetectorRef.detectChanges();
			});
	}

	ngAfterViewInit() {

		// turn on when structure is ready
		if (this.structureParent) {
			this.resizeDetector
				.observe(this.structureParent.getElementRef().nativeElement)
				.pipe(
					filter(() => this.autoResizeWidthEnabled),
					map((element: HTMLElement) => element.offsetWidth),
					distinctUntilChanged(),
					this.takeUntil()
				)
				.subscribe((width: number) => {
					this.recalculateContainer(width);
				});
		}

		this.compositionQueryService
			.onContainerWidth()
			.pipe(
				this.takeUntil()
			)
			.subscribe((width: number) => {
				this.renderer.setStyle(this.containerRef.nativeElement, 'width', width + 'px');
			});

		this.structureQueryService
			.onVerticalScrollEnabled()
			.pipe(
				this.takeUntil()
			)
			.subscribe((enabled: boolean) => {
				if (enabled) {
					this.enableScrollObservation();
				} else {
					this.disableScrollObservation();
				}

			});

		// never runs ??
		this.structureQueryService
			.onStructure()
			.pipe(
				filter((str: Structure) => {
					return str.isVerticalScrollEnabled();
				}),
				this.takeUntil()
			)
			.subscribe((structure: Structure) => {

				const containerHeight = structure.getContainerHeight(),
					topMargin = structure.getTopMargin();

				this.renderer.setStyle(this.containerRef.nativeElement, 'padding-top', topMargin + 'px');
				this.renderer.setStyle(this.containerRef.nativeElement, 'height', containerHeight + 'px');
			});
	}

	ngOnDestroy() {
		super.ngOnDestroy();
		this.resizeDetector.destroy(this.elementRef.nativeElement);
	}

	setHeight(height: number): void {
		this.height = height;
		this.renderer.setStyle(this.elementRef.nativeElement, 'height', this.height + 'px');
	}

	private recalculateContainer(width: number): void {
		if (this.autoResizeWidthEnabled) {
			this.compositionCommandService.setContainerWidth(width);
		}
	}

	private enableScrollObservation(): void {
		this.ngZone
			.runOutsideAngular(() => {

				fromEvent(this.elementRef.nativeElement, 'scroll')
					.pipe(
						// auditTime(10),
						takeUntil(this.scrollObservation$),
						this.takeUntil()
					)
					.subscribe((event: any) => {

						const scrollTop = event.target.scrollTop;

						this.structureCommandService.setScrollPosition(scrollTop);
					});
			});
	}

	private disableScrollObservation(): void {
		this.scrollObservation$.next();
		this.scrollObservation$.complete();
	}

}
