import { AggregateEvent } from '@generic-ui/hermes';

import { ColumnConfig } from '../../../../../../composition/domain/column.config';
import { StructureId } from '../../../../structure-id';
import { TypeField } from '../data-type/type.field';


export class FieldsInitedAggregateEvent extends AggregateEvent {

	constructor(structureId: StructureId,
				private readonly columns: Array<ColumnConfig>, // TODO merge
				private readonly fields: Array<TypeField>) {
		super(structureId, 'FieldsInitedAggregateEvent');
	}

	getColumns(): Array<ColumnConfig> {
		return this.columns;
	}

	getFields(): Array<TypeField> {
		return this.fields;
	}
}
