import { Component, Input, SimpleChanges } from '@angular/core';

import { CellTemplateWithAccessor } from '../../../../../composition/domain/read/definition/cell-template-with-accessor';
import { ChangedValueEmitter } from '../../../../../composition/domain/read/edit/changed-value.emitter';
import { EditEventType } from '../../../../../composition/domain/read/edit/state/edit.event-type';
import { ItemEntity } from '../../../../domain/source/item.entity';
import { SmartComponent } from '../../../../../../common/cdk/smart-component';
import { StructureEditSourceItemParams } from '../../../../domain/source/command/origin/edit/structure.edit-source-item.params';
import { SourceCommandService } from '../../../../ui-api/source/source-command.service';

@Component({
	selector: 'gui-structure-cell-edit[entity][cell]',
	template: `

		<span >
			<ng-container
					*ngTemplateOutlet="cell.editTemplate;
									context: editContext">
			</ng-container>
		</span>

	`
})
export class StructureCellEditComponent extends SmartComponent {

	@Input()
	entity: ItemEntity;

	@Input()
	cell: CellTemplateWithAccessor;

	editContext: any;

	valueChanges$: ChangedValueEmitter<any>;

	status$: ChangedValueEmitter<any>;

	actualValue: any;

	constructor(private readonly sourceCommandService: SourceCommandService) {
		super();
	}

	ngOnChanges(changes: SimpleChanges) {

		if (changes.entity !== null && changes.entity !== undefined) {
			this.initEditContext();
		}

		if (changes.cell !== null && changes.cell !== undefined) {
			this.initEditContext();
		}

	}

	ngOnInit() {
		this.initEditContext();
	}

	private submitChanges(): void {
		const itemId = this.entity.getId(),
			updatedValue = this.actualValue,
			fieldId = this.cell.columnFieldId;

		this.sourceCommandService.editItem(new StructureEditSourceItemParams(itemId, fieldId, updatedValue));

	}

	private initEditContext(): void {
		this.valueChanges$ = new ChangedValueEmitter();
		this.status$ = new ChangedValueEmitter<EditEventType>();

		this.editContext = {
			status: this.status$,
			valueChanges: this.valueChanges$,
			value: this.cell.getValue(this.entity),
			focus: false
		};

		this.status$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((status: EditEventType) => {

				switch (status) {
					case EditEventType.SUBMIT:
						this.submitChanges();
						break;

					default:
						break;
				}
			});
	}

}
