import { TemplateRef } from '@angular/core';

import { ColumnField } from '../../command/column/field/column-field';
import { SortOrder } from '../../command/column/sort/sort-order';
import { HeaderTemplate } from '../../column.config';
import { CellTemplateWithContext } from './cell-template-with-context';
import { CellContext } from './cell-context';
import { FieldId } from '../../../../structure/domain/structure/command/field/data-type/field.id';
import { CellTemplateWithAccessor } from './cell-template-with-accessor';
import { ColumnId } from '../../command/column/column.id';
import { DataType } from '../../../../structure/domain/structure/command/field/data-type/data-type';
import { ViewEntity } from '../../command/column/view.entity';
import { ColumnAlign } from '../../column-align';


export class ColumnDefinition {

	cellTemplate: TemplateRef<any>;

	headerTemplate: TemplateRef<any>;

	editTemplate: TemplateRef<any>;

	width: number;

	templateFunction: (i: any) => string;

	private readonly columnId: ColumnId;

	private field: ColumnField;

	private sortStatus: SortOrder;

	private sortable: boolean = true;

	private header: string | HeaderTemplate;

	private enabled: boolean;

	private cellEditingEnabled: boolean;

	constructor(field: ColumnField,
				columnId: ColumnId,
				enabled: boolean,
				private readonly type: DataType, // REFACTOR
				private readonly view: ViewEntity, // REFACTOR
				private readonly align: ColumnAlign, // REFACTOR
				header: string | HeaderTemplate,
				cellEditingEnabled: boolean,
				sort: SortOrder = SortOrder.NONE,
				sortable = true) {
		this.field = field;
		this.columnId = columnId;
		this.enabled = enabled;
		this.header = header;
		this.cellEditingEnabled = cellEditingEnabled;
		this.sortStatus = sort;
		this.sortable = sortable;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	setHeader(header: string): void {
		this.header = header;
	}

	setField(field: ColumnField): void {
		this.field = field;
	}

	getHeaderCellTemplateWithContext(index: number): CellTemplateWithContext {

		const header: any = this.header;
		let headerCellContext: CellContext;

		if (typeof header === 'string') {
			headerCellContext = new CellContext(header);
		} else if (typeof header === 'function') {
			headerCellContext = new CellContext(header(index));
		}

		const fieldId = new FieldId(this.field.getId().getId());

		return new CellTemplateWithContext(
			this.headerTemplate,
			this.editTemplate,
			headerCellContext,
			this.width,
			fieldId,
			this.columnId,
			this.sortStatus,
			this.enabled,
			this.sortable,
			this.align
		);
	}

	getContentCellTemplateWithAccessor(): CellTemplateWithAccessor {

		const accessor = (entity: any) => {
			return this.field.getAccessor()(entity);
		}, searchAccessor = (entity: any) => {
			return this.field.getSearchAccessor()(entity);
		};

		return new CellTemplateWithAccessor(
			this.cellTemplate,
			this.editTemplate,
			true,
			this.templateFunction,
			accessor,
			searchAccessor,
			this.width,
			this.field.getId(),
			this.align,
			this.cellEditingEnabled,
			this.type,
			this.view
		);
	}

}
