import { Injectable } from '@angular/core';

import { CompositionDispatcher } from '../domain/command/composition.dispatcher';
import { CompositionId } from '../domain/composition-id';
import { globalCompositionId } from '../domain/global-composition-id';
import { ColumnParams } from '../domain/command/column/set-columns/column.params';
import { ColumnId } from '../domain/command/column/column.id';


@Injectable()
export class CompositionCommandService {

	constructor(private compositionDispatcher: CompositionDispatcher) {
	}

	createComposition(compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.createComposition(compositionId);
	}

	setColumns(params: Array<ColumnParams>, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.setColumns(compositionId, params);
	}

	setWidth(width: number, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.setWidth(compositionId, width);
	}

	setContainerWidth(width: number, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.setContainerWidth(compositionId, width);
	}

	setResizeWidth(enabled: boolean, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.setResizeWidth(compositionId, enabled);
	}

	enableColumn(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.setColumnEnabled(compositionId, columnId, true);
	}

	disableColumn(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.setColumnEnabled(compositionId, columnId, false);
	}

	moveLeft(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.moveLeft(compositionId, columnId);
	}

	moveRight(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		this.compositionDispatcher.moveRight(compositionId, columnId);
	}

}
