import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

import { AggregateId } from '@generic-ui/hermes';

import { StructureRepository } from '../../domain/structure/read/structure.repository';
import { globalStructureId } from '../../domain/global-structure-id';
import { Structure } from '../../domain/structure/read/structure';
import { VerticalFormationRepository } from '../../domain/structure/read/vertical-formation/vertical-formation.repository';


@Injectable()
export class StructureReadModelService {

	constructor(private structureRepository: StructureRepository,
				private verticalFormationRepository: VerticalFormationRepository) {
	}

	onStructure(aggregateId: AggregateId = globalStructureId): Observable<Structure> {
		return this.structureRepository.onStructure(aggregateId);
	}

	onVerticalScrollEnabled(aggregateId: AggregateId = globalStructureId): Observable<boolean> {
		return this.structureRepository
				   .onStructure(aggregateId)
				   .pipe(
					   map((structure: Structure) => structure.isVerticalScrollEnabled()),
					   distinctUntilChanged()
				   );
	}

	onRowHeight(aggregateId: AggregateId = globalStructureId): Observable<number> {
		return this.verticalFormationRepository.onRowHeight(aggregateId);
	}

}
