import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';
import { StructureSourceDomainEventPublisher } from '../../../../source/command/structure.source.domain-event.publisher';
import { StructureSetAggregationEnabledCommand } from './structure.set-aggregation-enabled.command';
import { StructureAggregationEnabledSetEvent } from './structure.aggregation-enabled-set.event';
import { StructureAggregate } from '../../structure.aggregate';


@Injectable()
export class StructureSetAggregationEnabledCommandHandler implements CommandHandler<StructureAggregate, StructureSetAggregationEnabledCommand> {

	constructor(private readonly structureSourceDomainEventPublisher: StructureSourceDomainEventPublisher,
				private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<StructureSetAggregationEnabledCommand> {
		return StructureSetAggregationEnabledCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: StructureSetAggregationEnabledCommand): void {

		const enabled = command.isEnabled(),
			aggregateEvents = aggregate.getEvents();

		this.domainEventPublisher.publish(new StructureAggregationEnabledSetEvent(command.getAggregateId(), enabled));
		this.structureSourceDomainEventPublisher.publish(aggregateEvents);
	}

	handleAggregate(structure: StructureAggregate, command: StructureSetAggregationEnabledCommand): void {

		const enabled = command.isEnabled();

		structure.setAggregationEnabled(enabled);
	}

}
