import { ChangeDetectionStrategy, Component, Input, OnChanges, SimpleChanges } from '@angular/core';

import { Paging } from '../../../domain/paging/read/paging';


@Component({
	selector: 'gui-structure-paging-stats',
	templateUrl: `structure-paging-stats.component.html`,
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class StructurePagingStatsComponent implements OnChanges {

	@Input()
	paging: Paging;

	sourceSize: number;

	firstItemIndex: number;

	lastItemIndex: number;

	ngOnChanges(changes: SimpleChanges) {
		this.calculate();
	}

	calculate(): void {
		if (this.paging) {
			this.firstItemIndex = this.paging.getStart();
			this.lastItemIndex = this.paging.getEnd();
			this.sourceSize = this.paging.getSourceSize();
		}
	}

	isSourceNotEmpty(): boolean {
		return this.sourceSize > 0;
	}

}
