import { AggregateId, ReadModel } from '@generic-ui/hermes';

import { Formation } from '../../formation/read/formation';
import { Schema } from '../../schema/read/schema';
import { Paging } from '../../paging/read/paging';
import { Source } from '../../source/read/source';
import { ItemEntity } from '../../source/item.entity';
import { VerticalFormationReadModel } from './vertical-formation/vertical-formation.read-model';


export class Structure extends ReadModel {

	private readonly formation: Formation;

	private readonly schema: Schema;

	private readonly paging: Paging;

	private readonly entities: Array<ItemEntity>;

	private readonly source: Source;

	private readonly verticalFormation: VerticalFormationReadModel;

	constructor(uid: AggregateId,
				formation: Formation,
				schema: Schema,
				paging: Paging,
				entities: Array<ItemEntity>,
				source: Source,
				verticalFormation: VerticalFormationReadModel) {
		super(uid);
		this.formation = formation;
		this.schema = schema;
		this.paging = paging;
		this.entities = entities;
		this.source = source;
		this.verticalFormation = verticalFormation;
	}

	getFormation(): Formation {
		return this.formation;
	}

	getSchema(): Schema {
		return this.schema;
	}

	getPaging(): Paging {
		return this.paging;
	}

	getEntities(): Array<ItemEntity> {
		return this.entities;
	}

	getSource(): Source {
		return this.source;
	}

	getVerticalFormation(): VerticalFormationReadModel {
		return this.verticalFormation;
	}

	getSelectedEntities(): Array<ItemEntity> {

		const selectedIds = this.formation.getSelectedRows().getIds();

		return this.entities.filter((entity: ItemEntity) => {
			return selectedIds.some((id: string) => entity.equalsById(id));
		});
	}

	getTopMargin(): number {
		return this.verticalFormation.getTopMargin();
	}

	getContainerHeight(): number {
		return this.verticalFormation.getContainerHeight();
	}

	isLoaderVisible(): boolean {
		if (this.getSchema().getHeight() > 150) {
			return this.getSource().isLoading();
		} else {
			return false;
		}
	}

	isTopHeaderEnabled(): boolean {
		return this.schema.getColumnHeader().isTopHeaderEnabled();
		// && this.presentation.getColumns().length > 0; // TODO Move schema.topHeader to presentation
	}

	isBottomHeaderEnabled(): boolean {
		return this.schema.getColumnHeader().isBottomHeaderEnabled();
		// && this.presentation.getColumns().length > 0; // TODO Move schema.bottomHeader to presentation
	}

	isReadyToDisplay(): boolean {
		return this.entities.length > 0;
		// this.presentation.getColumns().length > 0; // TODO presentation getColumns
	}

	isVerticalScrollEnabled(): boolean {
		return this.verticalFormation.isEnabled();
	}

}

