import { Field } from '../field';
import { FieldId } from '../field.id';
import { ColumnAggregationConfig } from '../../../../../../../composition/domain/column-aggregation.config';
import { DataType } from '../data-type';
import { AggregationType } from '../../../aggregation/aggregation.type';
import { FieldMatchers } from '../../matcher/field.matchers';
import { OriginItemEntity } from '../../../../../source/command/origin/origin-item-entity';


export class StringField extends Field<string> {

	constructor(id: FieldId,
				accessor: any,
				matchers: FieldMatchers,
				aggregationConfig?: ColumnAggregationConfig) {
		super(id,
			accessor,
			DataType.STRING,
			matchers,
			aggregationConfig);
	}

	assignDefaultAggregations(): AggregationType {
		return 0;
		// return AggregationType.COUNT |
		// 	AggregationType.DISTINCT;
	}

	assignPossibleAggregations(): AggregationType {
		return AggregationType.COUNT |
			AggregationType.DISTINCT;
	}

	search(item: OriginItemEntity, searchPhrase: string): boolean {
		const value = this.getSearchValue(item);

		if (typeof value === 'string') {
			return value.toLowerCase().indexOf(searchPhrase.toLowerCase()) > -1;
		} else {
			return false;
		}
	}

	sort(strOne: string, strTwo: string): number {
		const sureStrOne = '' + strOne,
			sureStrTwo = '' + strTwo;

		return sureStrOne.localeCompare(sureStrTwo);
	}

	filter(value: string, filterPhrase: string): boolean {
		return value.toLowerCase().indexOf(filterPhrase.toLowerCase()) > -1;
	}

}
