import { Injectable } from '@angular/core';

import { InMemoryReadModelStore } from '@generic-ui/hermes';

import { Structure } from '../../../../domain/structure/read/structure';
import { StructureAggregate } from '../../../../domain/structure/command/structure.aggregate';
import { InMemoryStructureStore } from '../in-memory.structure.store';
import { StructureConverter } from '../../../../domain/structure/structure.converter';


@Injectable()
export class InMemoryStructureReadStore extends InMemoryReadModelStore<Structure, StructureAggregate> {

	constructor(private inMemoryProjectStore: InMemoryStructureStore,
				private structureConverter: StructureConverter) {
		super(inMemoryProjectStore);
	}

	toReadModel(aggregate: StructureAggregate): Structure {
		return this.structureConverter.convert(aggregate);
	}

}
