import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { take } from 'rxjs/operators';

import { PagingRepository } from '../../domain/paging/read/paging.repository';
import { StructureId } from '../../domain/structure-id';
import { globalStructureId } from '../../domain/global-structure-id';
import { Paging } from '../../domain/paging/read/paging';


@Injectable()
export class PagingReadModelService {

	constructor(private pagingRepository: PagingRepository) {
	}

	onPaging(structureId: StructureId = globalStructureId): Observable<Paging> {
		return this.pagingRepository.on(structureId);
	}

	onSinglePaging(structureId: StructureId = globalStructureId): Observable<Paging> {
		return this.onPaging(structureId)
				   .pipe(
					   take(1)
				   );
	}

}
