import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { FormationRepository } from '../../domain/formation/read/formation-repository';
import { StructureId } from '../../domain/structure-id';
import { globalStructureId } from '../../domain/global-structure-id';
import { SelectedRowsList } from '../../domain/formation/read/selected-rows/selected-rows-list';
import { Formation } from '../../domain/formation/read/formation';


@Injectable()
export class FormationReadModelService {

	constructor(private formationRepository: FormationRepository) {
	}

	onSelectedRows(structureId: StructureId = globalStructureId): Observable<SelectedRowsList> {
		return this.formationRepository
				   .onFormation(structureId)
				   .pipe(
					   map((formation: Formation) => formation.getSelectedRows())
				   );
	}

}
