import { ColumnHeaderEntity } from './column-header/column-header.entity';
import { SchemaTheme } from '../schema-theme';
import { SchemaRowColoring } from '../schema-row-coloring';
import { RootAggregate } from '@generic-ui/hermes';

@RootAggregate
export class SchemaAggregate {

	private headerHeight = 28; // ?

	private pagerHeight = 35; // ?

	private containerHeight: number;

	private active: boolean = false;

	private horizontalGrid: boolean = true;

	private verticalGrid: boolean = true;

	private columnHeader: ColumnHeaderEntity = new ColumnHeaderEntity(true, false);

	private theme: SchemaTheme = SchemaTheme.GENERIC;

	private rowColoring: SchemaRowColoring = SchemaRowColoring.ODD;

	constructor(private width?: number,
				private height?: number) {
		this.calculateContainerHeight();
	}

	getRowColoring(): SchemaRowColoring {
		return this.rowColoring;
	}

	setHeight(height: number): void {
		this.height = height;

		this.calculateContainerHeight();
	}

	setTheme(theme: SchemaTheme): void {
		this.theme = theme;

		if (theme === SchemaTheme.MATERIAL) {
			this.rowColoring = SchemaRowColoring.NONE;
			this.verticalGrid = false;
		}

		if (theme === SchemaTheme.LIGHT) {
			this.rowColoring = SchemaRowColoring.NONE;
			this.verticalGrid = false;
		}

		if (theme === SchemaTheme.DARK) {
			this.rowColoring = SchemaRowColoring.NONE;
			this.verticalGrid = false;
		}

		if (theme === SchemaTheme.GENERIC) {
			this.rowColoring = SchemaRowColoring.ODD;
		}
	}

	setRowColoring(coloring: SchemaRowColoring): void {
		this.rowColoring = coloring;
	}

	getTheme(): SchemaTheme {
		return this.theme;
	}

	getWidth(): number {
		return this.width;
	}

	getHeight(): number {
		return this.height;
	}

	getContainerHeight(): number {
		return this.height;
		// return this.containerHeight;
	}

	getColumnHeader(): ColumnHeaderEntity {
		return this.columnHeader;
	}

	setHeaderTop(enabled: boolean): void {
		this.getColumnHeader().setHeaderTop(enabled);
	}

	setHeaderBottom(enabled: boolean): void {
		this.getColumnHeader().setHeaderBottom(enabled);
	}

	setVerticalGrid(enabled: boolean): void {
		this.verticalGrid = enabled;
	}

	setHorizontalGrid(enabled: boolean): void {
		this.horizontalGrid = enabled;
	}

	isVerticalGridEnabled(): boolean {
		return this.verticalGrid;
	}

	isHorizontalGridEnabled(): boolean {
		return this.horizontalGrid;
	}

	private getHeaderHeight(): number {
		return this.headerHeight;
	}

	private getPagerHeight(): number {
		return this.pagerHeight;
	}

	private calculateContainerHeight(): void {

		if (!this.height) {
			this.active = false;
			return;
		} else {
			this.active = true;
		}

		this.containerHeight = this.height - this.getHeaderHeight() - this.getPagerHeight();
	}

}
