import { AggregateId, DomainEvent } from '@generic-ui/hermes';

import { OriginItemEntity } from '../origin-item-entity';


export class OriginSetEvent extends DomainEvent {

	constructor(aggregateId: AggregateId,
				private readonly origin: Array<OriginItemEntity>) {
		super(aggregateId, 'OriginSetEvent');
	}

	getOrigin(): Array<OriginItemEntity> {
		return this.origin;
	}
}
