import { TemplateRef } from '@angular/core';

import { ColumnFieldId } from '../../command/column/field/column-field.id';
import { ItemEntity } from '../../../../structure/domain/source/item.entity';
import { DataType } from '../../../../structure/domain/structure/command/field/data-type/data-type';
import { ViewEntity } from '../../command/column/view.entity';
import { CellView } from '../../cell-view';
import { ColumnAlign } from '../../column-align';


export class CellTemplateWithAccessor {

	constructor(public template: TemplateRef<any>,
				public editTemplate: TemplateRef<any>,
				public editable: boolean,
				public templateFun: (i: any) => any,
				public accessor: (element: any) => any,
				public searchAccessor: (element: any) => any,
				public width: number,
				public columnFieldId: ColumnFieldId,
				private readonly align: ColumnAlign,
				private readonly cellEditingEnabled: boolean,
				private readonly type: DataType, // REFACTOR
				private readonly view: ViewEntity) { // REFACTOR
	}

	isCellEditingEnabled(): boolean {
		return this.cellEditingEnabled;
	}

	isBooleanDataType(): boolean {
		return this.type === DataType.BOOLEAN;
	}

	isAlignLeft(): boolean {
		return this.align === ColumnAlign.LEFT;
	}

	isAlignCenter(): boolean {
		return this.align === ColumnAlign.CENTER;
	}

	isAlignRight(): boolean {
		return this.align === ColumnAlign.RIGHT;
	}

	// REFACTOR #1581
	getValue(entity: ItemEntity, searchPhrase?: string): any | HTMLElement {

		return this.templateFun(this.findValue(entity, searchPhrase));
	}

	private findValue(entity: ItemEntity, searchPhrase?: string): any | HTMLElement {
		const value = this.accessor(entity);

		if (this.type !== DataType.STRING) {
			return value;
		}

		if (this.view && this.view.getCellView() === CellView.FUNCTION) {
			return value;
		}

		if (searchPhrase) {

			let txt: string = '' + this.searchAccessor(entity),
				lowerCasedText: string = txt.toLocaleLowerCase(),
				loweredSearchPhrase = searchPhrase.toLocaleLowerCase();

			let positions: Array<number> = [];

			let index = 0;

			while (index < loweredSearchPhrase.length) {

				const found = lowerCasedText.indexOf(loweredSearchPhrase, index);

				if (found >= 0) {
					positions.push(found);
					index = found + searchPhrase.length;
					continue;
				} else {
					index = loweredSearchPhrase.length;
				}
			}

			const arr = txt.split('');

			positions.forEach((pos: number) => {

				for (let i = 0; i < searchPhrase.length; i += 1) {

					let ph;

					if (i === 0) {
						ph = `<span class="gui-text-highlight">${arr[pos + i]}`;
					} else {
						ph = arr[pos + i];
					}

					if (i === searchPhrase.length - 1) {
						ph += `</span>`;
					}

					arr[pos + i] = ph;
				}

			});

			txt = arr.join('');

			return txt;
		} else {
			return this.accessor(entity);
		}
	}

}
