import { Injectable } from '@angular/core';

import { Override } from '../../../../../common/cdk/decorators';

import { CompositionCommandService } from '../../../../composition/ui-api/composition.command-service';
import { CompositionId } from '../../../../composition/domain/composition-id';
import { CompositionDispatcher } from '../../../../composition/domain/command/composition.dispatcher';
import { globalCompositionId } from '../../../../composition/domain/global-composition-id';
import { ColumnParams } from '../../../../composition/domain/command/column/set-columns/column.params';
import { ColumnId } from '../../../../composition/domain/command/column/column.id';


@Injectable()
export class LocalCompositionCommandService extends CompositionCommandService {

	constructor(private readonly compositionId: CompositionId,
				compositionDispatcher: CompositionDispatcher) {
		super(compositionDispatcher);
	}

	@Override
	createComposition(compositionId: CompositionId = globalCompositionId): void {
		super.createComposition(this.compositionId);
	}

	@Override
	setColumns(params: Array<ColumnParams>): void {
		super.setColumns(params, this.compositionId);
	}

	@Override
	setWidth(width: number, compositionId: CompositionId = globalCompositionId): void {
		super.setWidth(width, this.compositionId);
	}

	@Override
	setContainerWidth(width: number, compositionId: CompositionId = globalCompositionId): void {
		super.setContainerWidth(width, this.compositionId);
	}

	@Override
	setResizeWidth(enabled: boolean, compositionId: CompositionId = globalCompositionId): void {
		super.setResizeWidth(enabled, this.compositionId);
	}

	@Override
	enableColumn(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		super.enableColumn(columnId, this.compositionId);
	}

	@Override
	disableColumn(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		super.disableColumn(columnId, this.compositionId);
	}

	@Override
	moveLeft(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		super.moveLeft(columnId, this.compositionId);
	}

	@Override
	moveRight(columnId: ColumnId, compositionId: CompositionId = globalCompositionId): void {
		super.moveRight(columnId, this.compositionId);
	}

}
