import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSchemaHorizontalGridCommand } from './set-schema-horizontal-grid.command';
import { SchemaHorizontalGridSetEvent } from './schema-horizontal-grid-set.event';
import { StructureAggregate } from '../../../../structure/command/structure.aggregate';


@Injectable()
export class SetSchemaHorizontalGridCommandHandler implements CommandHandler<StructureAggregate, SetSchemaHorizontalGridCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetSchemaHorizontalGridCommand> {
		return SetSchemaHorizontalGridCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetSchemaHorizontalGridCommand): void {
		this.domainEventPublisher.publish(new SchemaHorizontalGridSetEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetSchemaHorizontalGridCommand): void {

		const schema = aggregate.getSchema(),
			enabled = command.isEnabled();

		schema.setHorizontalGrid(enabled);
	}
}
