import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { StructureRepository } from '../../structure/read/structure.repository';
import { StructureId } from '../../structure-id';
import { Paging } from './paging';
import { Structure } from '../../structure/read/structure';


@Injectable()
export class PagingRepository {

	constructor(private structureRepository: StructureRepository) {
	}

	on(structureId: StructureId): Observable<Paging> {

		return this.structureRepository
				   .onStructure(structureId)
				   .pipe(
					   filter((structure: Structure) => structure.getId().toString() === structureId.toString()),
					   map((structure: Structure) => structure.getPaging())
				   );
	}

}
