import { AggregateEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../structure-id';
import { FieldId } from '../field/data-type/field.id';
import { AggregationCalculator } from './calculation/aggregation.calculator';
import { OriginItemEntity } from '../../../source/command/origin/origin-item-entity';
import { StructureAggregationsChangedAggregateEvent } from './structure.aggregations-changed.aggregate-event';
import { TypeField } from '../field/data-type/type.field';


export class AggregationManager {

	private readonly structureId: StructureId;

	private enabled: boolean = false;

	private readonly values = new Map<FieldId, any>();

	constructor(structureId: StructureId,
				private readonly calculators: Array<AggregationCalculator<any, any>>) {
		this.structureId = structureId;
	}

	calculate(fields: Array<TypeField>, entities: Array<OriginItemEntity>): Array<AggregateEvent> {

		if (!this.enabled) {
			return [];
		}

		const calculations = new Map();

		this.calculators.forEach((calc: AggregationCalculator<any, any>) => {

			const allFields = fields.map((typeField: TypeField) => typeField.getField());

			const aggregatedValues = calc.calculate(allFields, entities);

			if (aggregatedValues) {
				Array.from(aggregatedValues.keys())
					 .forEach((key: any) => {
						 calculations.set(key, aggregatedValues.get(key));
					 });
			}
		});

		if (calculations.size > 0) {
			return [
				new StructureAggregationsChangedAggregateEvent(this.structureId, calculations)
			];
		} else {
			return [];
		}

	}

	setEnabled(enabled: boolean): void {
		this.enabled = enabled;
	}

	add() {
	}

	remove() {
	}

	update() {
	}

}
