import { AggregateId, DomainEvent } from '@generic-ui/hermes';

export class StructureSearchPhraseSetDomainEvent extends DomainEvent {

	constructor(aggregateId: AggregateId,
				private readonly phrase: string) {
		super(aggregateId, 'StructureSearchPhraseSetDomainEvent');
	}

	getPhrase(): string {
		return this.phrase;
	}

}
