import { EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { skip } from 'rxjs/operators';

import { SmartComponent } from '../../../../common/cdk/smart-component';
import { ColumnConfig } from '../../../composition/domain/column.config';
import { PagingConfig } from '../../domain/paging/paging-config';
import { SchemaTheme } from '../../domain/schema/schema-theme';
import { SchemaRowColoring } from '../../domain/schema/schema-row-coloring';
import { SortingConfig } from '../../ui-api/structure/sorting-config';
import { FilterConfig } from '../../ui-api/structure/filter/filter-config';
import { QuickFiltersConfig } from '../../ui-api/structure/filter/quick-filters.config';
import { SearchConfig } from '../../ui-api/structure/search/search-config';
import { AggregationConfig } from '../../ui-api/structure/aggregation/aggregation-config';
import { EditemItemValues } from '../../ui-api/source/event/editem-item.values';
import { StructureId } from '../../domain/structure-id';
import { CompositionId } from '../../../composition/domain/composition-id';
import { StructureCommandService } from '../../ui-api/structure/structure-command.service';
import { PagingCommandService } from '../../ui-api/paging/paging-command.service';
import { PagingEventService } from '../../ui-api/paging/paging-event.service';
import { SourceCommandService } from '../../ui-api/source/source-command.service';
import { SourceEventService } from '../../ui-api/source/event/source-event.service';
import { SchemaCommandService } from '../../ui-api/schema/schema-command.service';
import { CompositionCommandService } from '../../../composition/ui-api/composition.command-service';
import { CompositionEventService } from '../../../composition/ui-api/composition.event-service';
import { FormationEventService } from '../../ui-api/formation/formation-event.service';
import { StructureEditModeArchive } from '../edit/structure.edit-mode.archive';
import { StructureCellEditArchive } from '../edit/structure.cell-edit.archive';
import { StructureInfoPanelEnabledArchive } from '../panel/info/structure.info-panel-enabled.archive';
import { StructureAggregationConfigService } from '../panel/aggregation/structure.aggregation-config.service';
import { StructureCellEditStore } from '../edit/structure.cell-edit.store';
import { StructureCellEditState } from '../edit/structure.cell-edit.state';
import { ColumnMenuConfig } from '../../ui-api/structure/column-menu-config';
import { StructureColumnMenuConfigArchive } from '../header/menu/config/structure.column-menu-config.archive';
import { StructurePagingDisplayModeArchive } from '../paging/mode/structure-paging.display-mode.archive';
import { StructureRowSelectEnabledArchive } from '../content/row/structure.row-select-enabled.archive';
import { StructureSearchEventService } from '../../ui-api/structure/search/structure-search-event.service';


/** @internal */
export abstract class StructureGateway extends SmartComponent implements OnChanges, OnInit {

	/***********************
	 * INPUTS
	 ***********************/

	@Input()
	columnHeaderTop: boolean;

	@Input()
	columnHeaderBottom: boolean;

	@Input()
	maxHeight: number;

	@Input()
	width: number;

	@Input()
	rowHeight: number;

	@Input()
	autoResizeWidth: boolean;

	@Input()
	source: Array<any> = [];

	@Input()
	columns: Array<ColumnConfig> = [];

	@Input()
	paging: boolean | PagingConfig;

	@Input()
	verticalGrid: boolean;

	@Input()
	horizontalGrid: boolean;

	@Input()
	theme: SchemaTheme;

	@Input()
	rowColoring: SchemaRowColoring;

	@Input()
	rowSelecting: boolean;

	@Input()
	loading: boolean;

	@Input()
	virtualScroll: boolean;

	@Input()
	sorting: boolean | SortingConfig;

	@Input()
	filtering: boolean | FilterConfig;

	@Input()
	quickFilters: boolean | QuickFiltersConfig;

	@Input()
	searching: boolean | SearchConfig;

	@Input()
	editMode: boolean;

	@Input()
	cellEditing: boolean;

	@Input()
	infoPanel: boolean;

	@Input()
	aggregation: AggregationConfig;

	@Input()
	columnMenu: ColumnMenuConfig;


	/***********************
	 * OUTPUTS
	 ***********************/

	@Output()
	pageChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	pageSizeChanged: EventEmitter<number> = new EventEmitter<number>();

	@Output()
	itemsSelected: EventEmitter<any> = new EventEmitter();

	@Output()
	columnsChanged: EventEmitter<void> = new EventEmitter();

	@Output()
	containerWidthChanged: EventEmitter<number> = new EventEmitter();

	@Output()
	sourceEdited: EventEmitter<EditemItemValues> = new EventEmitter();

	@Output()
	cellEditEntered: EventEmitter<void> = new EventEmitter();

	@Output()
	cellEditCanceled: EventEmitter<void> = new EventEmitter();

	@Output()
	cellEditSubmitted: EventEmitter<void> = new EventEmitter();

	@Output()
	searchPhraseChanged: EventEmitter<string> = new EventEmitter();

	protected constructor(
		public structureId: StructureId,
		protected compositionId: CompositionId,
		protected structureCommandService: StructureCommandService,
		protected pagingCommandService: PagingCommandService,
		protected pagingEventService: PagingEventService,
		protected sourceCommandService: SourceCommandService,
		protected sourceEventService: SourceEventService,
		protected schemaCommandService: SchemaCommandService,
		protected compositionCommandService: CompositionCommandService,
		protected compositionEventService: CompositionEventService,
		protected formationEventService: FormationEventService,
		protected structureEditModeArchive: StructureEditModeArchive,
		protected structureCellEditArchive: StructureCellEditArchive,
		protected structureInfoPanelEnabledArchive: StructureInfoPanelEnabledArchive,
		protected structureAggregationConfigService: StructureAggregationConfigService,
		protected structureCellEditStore: StructureCellEditStore,
		protected structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
		protected structurePagingDisplayModeArchive: StructurePagingDisplayModeArchive,
		protected structureRowSelectEnabledArchive: StructureRowSelectEnabledArchive,
		protected structureSearchEventService: StructureSearchEventService
	) {
		super();
	}

	ngOnChanges(simpleChanges: SimpleChanges) {

		if (simpleChanges.theme !== undefined && simpleChanges.theme.currentValue !== undefined) {
			this.schemaCommandService.setTheme(this.theme);
		}

		if (simpleChanges.columnMenu !== undefined && simpleChanges.columnMenu.currentValue !== undefined) {
			this.structureColumnMenuConfigArchive.nextConfig(this.columnMenu);
		}

		if (simpleChanges.rowSelecting !== undefined && simpleChanges.rowSelecting.currentValue !== undefined) {
			this.structureRowSelectEnabledArchive.next(this.rowSelecting);
		}

		if (simpleChanges.infoPanel !== undefined && simpleChanges.infoPanel.currentValue !== undefined) {
			this.structureInfoPanelEnabledArchive.next(this.infoPanel);
		}

		if (simpleChanges.aggregation !== undefined && simpleChanges.aggregation.currentValue !== undefined) {
			this.structureAggregationConfigService.set(this.aggregation);
		}

		if (simpleChanges.editMode !== undefined && simpleChanges.editMode.currentValue !== undefined) {
			this.structureEditModeArchive.next(this.editMode);
		}

		if (simpleChanges.cellEditing !== undefined && simpleChanges.cellEditing.currentValue !== undefined) {
			this.structureCellEditArchive.next(this.cellEditing);
		}

		if (simpleChanges.width !== undefined && simpleChanges.width.currentValue !== undefined) {
			this.compositionCommandService.setWidth(this.width);
		}

		if (simpleChanges.rowHeight !== undefined && simpleChanges.rowHeight.currentValue !== undefined) {
			this.structureCommandService.setRowHeight(this.rowHeight);
		}

		if (simpleChanges.autoResizeWidth !== undefined && simpleChanges.autoResizeWidth.currentValue !== undefined) {
			this.compositionCommandService.setResizeWidth(this.autoResizeWidth);
		}

		if (simpleChanges.columnHeaderTop !== undefined && simpleChanges.columnHeaderTop.currentValue !== undefined) {
			this.schemaCommandService.changeTopHeader(this.columnHeaderTop);
		}

		if (simpleChanges.columnHeaderBottom !== undefined && simpleChanges.columnHeaderBottom.currentValue !== undefined) {
			this.schemaCommandService.changeBottomHeader(this.columnHeaderBottom);
		}

		if (simpleChanges.loading !== undefined && simpleChanges.loading.currentValue !== undefined) {
			this.sourceCommandService.setLoading(this.loading);
		}

		if (simpleChanges.paging !== undefined && simpleChanges.paging.currentValue !== undefined) {

			let pagingConfig: PagingConfig;

			if (typeof this.paging === 'boolean') {
				pagingConfig = {
					enabled: this.paging
				};
			} else {
				pagingConfig = this.paging;

				if (this.paging.displayMode !== undefined) {
					this.structurePagingDisplayModeArchive.next(this.paging.displayMode);
				}
			}

			this.pagingCommandService.setPaging(pagingConfig);
		}

		if (simpleChanges.columns) {
			this.structureCommandService.initFields(this.columns, this.compositionId, this.structureId);
			// this.compositionCommandService.setColumns(this.columns);
		}

		if (simpleChanges.verticalGrid !== undefined && simpleChanges.verticalGrid.currentValue !== undefined) {
			this.schemaCommandService.changeVerticalGrid(this.verticalGrid);
		}

		if (simpleChanges.horizontalGrid !== undefined && simpleChanges.horizontalGrid.currentValue !== undefined) {
			this.schemaCommandService.changeHorizontalGrid(this.horizontalGrid);
		}

		if (simpleChanges.rowColoring !== undefined && simpleChanges.rowColoring.currentValue !== undefined) {
			this.schemaCommandService.setRowColoring(this.rowColoring);
		}

		if (simpleChanges.maxHeight && this.maxHeight) {
			this.schemaCommandService.setHeight(this.maxHeight);
		}

		if (simpleChanges.virtualScroll !== undefined && simpleChanges.virtualScroll.currentValue !== undefined) {
			if (this.virtualScroll) {
				this.structureCommandService.enableVerticalScroll();
			} else {
				this.structureCommandService.disableVerticalScroll();
			}
		}

		if (simpleChanges.sorting !== undefined && simpleChanges.sorting.currentValue !== undefined) {

			let sorting: SortingConfig;

			if (typeof this.sorting === 'boolean') {
				sorting = {
					enabled: this.sorting
				};
			} else {
				sorting = this.sorting;
			}

			this.structureCommandService.setSortingConfig(sorting);
		}

		if (simpleChanges.filtering !== undefined && simpleChanges.filtering.currentValue !== undefined) {

			let filtering: FilterConfig;

			if (typeof this.filtering === 'boolean') {
				filtering = {
					enabled: this.filtering
				};
			} else {
				filtering = this.filtering;
			}

			this.structureCommandService.setFilterConfig(filtering);
		}

		if (simpleChanges.quickFilters !== undefined && simpleChanges.quickFilters.currentValue !== undefined) {

			let quickFilters: QuickFiltersConfig;

			if (typeof this.quickFilters === 'boolean') {
				quickFilters = {
					enabled: this.quickFilters
				};
			} else {
				quickFilters = this.quickFilters;
			}

			this.structureCommandService.setQuickFiltersConfig(quickFilters);
		}

		if (simpleChanges.searching !== undefined && simpleChanges.searching.currentValue !== undefined) {

			let searching: SearchConfig;

			if (typeof this.searching === 'boolean') {
				searching = {
					enabled: this.searching
				};
			} else {
				searching = this.searching;
			}

			this.structureCommandService.setSearchingConfig(searching);
		}

		/**
		 * Setting source should be last step
		 */
		if (simpleChanges.source) {
			this.sourceCommandService.setOrigin(this.source);
		}
	}

	ngOnInit() {

		this.pagingEventService
			.onPageChange(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((pageNumber: number) => {
				this.pageChanged.emit(pageNumber);
			});

		this.pagingEventService
			.onPageSizeChange(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((pageSize: number) => {
				this.pageSizeChanged.emit(pageSize);
			});

		this.formationEventService
			.onItemSelected(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((items: Array<any>) => {
				this.itemsSelected.emit(items);
			});

		this.compositionEventService
			.onColumnsChanged(this.compositionId)
			.pipe(
				skip(1), // TODO system should emit response that it is ready
				this.takeUntil())
			.subscribe(() => {
				this.columnsChanged.emit();
			});


		this.compositionEventService
			.onContainerWidthChanged(this.compositionId)
			.pipe(
				// skip(1), // TODO system should emit response that it is ready
				this.takeUntil()
			)
			.subscribe((containerWidth: number) => {
				this.containerWidthChanged.emit(containerWidth);
			});

		this.sourceEventService
			.onSourceEdited(this.structureId)
			.pipe(
				// this.takeUntil()
			)
			.subscribe((values: EditemItemValues) => {
				this.sourceEdited.emit(values);
			});

		this.structureCellEditStore
			.onValue()
			.pipe(this.takeUntil())
			.subscribe((state: StructureCellEditState) => {

				switch (state) {

					case StructureCellEditState.ENTER:
						this.cellEditEntered.emit();
						break;

					case StructureCellEditState.SUBMIT:
						this.cellEditSubmitted.emit();
						break;

					case StructureCellEditState.CANCEL:
						this.cellEditCanceled.emit();
						break;
				}

			});

		this.structureSearchEventService
			.onSearchPhrase(this.structureId)
			.pipe(this.takeUntil())
			.subscribe((phrase: string) => {
				this.searchPhraseChanged.emit(phrase);
			});


	}

	onPageChange(page: number): void {
		this.pageChanged.emit(page);
	}

}
