import { Injectable, Type } from '@angular/core';

import { CommandHandler, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSchemaCommand } from './set-schema.command';
import { SchemaSetEvent } from './schema-set.event';
import { StructureAggregate } from '../../../structure/command/structure.aggregate';


@Injectable()
export class SetSchemaCommandHandler implements CommandHandler<StructureAggregate, SetSchemaCommand> {

	constructor(private domainEventPublisher: DomainEventPublisher) {
	}

	forCommand(): Type<SetSchemaCommand> {
		return SetSchemaCommand;
	}

	publishDomainEvents(aggregate: StructureAggregate, command: SetSchemaCommand): void {
		this.domainEventPublisher.publish(new SchemaSetEvent(command.getAggregateId()));
	}

	handleAggregate(aggregate: StructureAggregate, command: SetSchemaCommand): void {

		const height = command.getHeight();

		aggregate.setHeight(height);
	}

}
