import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';

import { DomainEvent, DomainEventBus } from '@generic-ui/hermes';

import { CompositionId } from '../domain/composition-id';
import { CompositionColumnsSetEvent } from '../domain/command/column/set-columns/composition.columns-set.event';
import { CompositionContainerWidthSetEvent } from '../domain/command/width/set-container-width/composition-container-width-set.event';


@Injectable()
export class CompositionEventService {

	constructor(private readonly domainEventBus: DomainEventBus) {
	}

	onColumnsChanged(compositionId: CompositionId): Observable<DomainEvent> {

		return this.domainEventBus
				   .ofEvent(
					   CompositionColumnsSetEvent
				   )
				   .pipe(
					   filter((event: DomainEvent) => event.getAggregateId().toString() === compositionId.toString())
				   );
	}

	// onWidthChanged(compositionId: CompositionId): Observable<number> {
	// 	return this.domainEventBus
	// 			   .ofEvent(
	// 				   CompositionColumnsSetEvent
	// 			   )
	// 			   .pipe(
	// 				   filter((event: DomainEvent) => event.getAggregateId().toString() === compositionId.toString())
	// 			   );
	// }

	onContainerWidthChanged(compositionId: CompositionId): Observable<number> {
		return this.domainEventBus
				   .ofEvent(
					   CompositionContainerWidthSetEvent
				   )
				   .pipe(
					   filter((event: DomainEvent) => event.getAggregateId().equals(compositionId)),
					   map((event: CompositionContainerWidthSetEvent) => {
						   return event.getPayload();
					   }),
					   filter((width: number) => !!width)
				   );
	}

}
