import { Command } from '@generic-ui/hermes';

import { StructureId } from '../../../../structure-id';
import { SortingConfig } from '../../../../../ui-api/structure/sorting-config';


export class SetSortingCommand extends Command {

	constructor(structureId: StructureId,
				private readonly sortingConfig: SortingConfig) {
		super(structureId, 'SetSortingCommand');
	}

	getSortingConfig(): SortingConfig {
		return this.sortingConfig;
	}

}
