import { Provider, Type } from '@angular/core';
import { AGGREGATION_CALCULATORS } from './calculation/aggregation.calculators.token';
import { BooleanAggregationCalculator } from './calculation/boolean/boolean-aggregation.calculator';
import { DateAggregationCalculator } from './calculation/date/date-aggregation.calculator';
import { NumberAggregationCalculator } from './calculation/number/number-aggregation.calculator';
import { StringAggregationCalculator } from './calculation/string/string-aggregation.calculator';
import { UnknownAggregationCalculator } from './calculation/unknown/unknown-aggregation.calculator';
import { AggregationManagerFactory } from './aggregation.manager.factory';


export function provideAggregationCalculator(clazz: Type<any>): Provider {
	return {
		provide: AGGREGATION_CALCULATORS,
		useClass: clazz,
		multi: true
	};
}

export const aggregationProviders = [
	provideAggregationCalculator(BooleanAggregationCalculator),
	provideAggregationCalculator(DateAggregationCalculator),
	provideAggregationCalculator(NumberAggregationCalculator),
	provideAggregationCalculator(StringAggregationCalculator),
	provideAggregationCalculator(UnknownAggregationCalculator),

	AggregationManagerFactory
];
