import { Injectable } from '@angular/core';

import { PagingAggregateFactory } from '../../paging/command/paging.aggregate-factory';
import { SourceManagerFactory } from '../../source/command/source.manager-factory';
import { VerticalFormationFactory } from './vertical-formation/vertical-formation.factory';
import { AggregationManagerFactory } from './aggregation/aggregation.manager.factory';
import { FilterManagerFactory } from './filter/filter.manager-factory';
import { FieldCollectionFactory } from './field/field-collection.factory';
import { StructureId } from '../../structure-id';
import { StructureAggregate } from './structure.aggregate';
import { SorterCollection } from './sort/sorter.collection';
import { SchemaAggregateFactory } from '../../schema/command/schema.aggregate-factory';
import { FormationAggregateFactory } from '../../formation/command/formation.aggregate-factory';
import { SearchManagerFactory } from './search/search.manager-factory';
import { AggregateFactory } from '@generic-ui/hermes';


@Injectable()
export class StructureAggregateFactory extends AggregateFactory<StructureAggregate> {

	constructor(private readonly pagingAggregateFactory: PagingAggregateFactory,
				private readonly sourceManagerFactory: SourceManagerFactory,
				private readonly formationAggregateFactory: FormationAggregateFactory,
				private readonly verticalFormationFactory: VerticalFormationFactory,
				private readonly aggregationManagerFactory: AggregationManagerFactory,
				private readonly filterManagerFactory: FilterManagerFactory,
				private readonly searchManagerFactory: SearchManagerFactory,
				private readonly fieldCollectionFactory: FieldCollectionFactory,
				private readonly schemaAggregateFactory: SchemaAggregateFactory) {
		super();
	}

	create(structureId: StructureId): StructureAggregate {

		const paging = this.pagingAggregateFactory.createDefault(),
			source = this.sourceManagerFactory.createDefault(),
			verticalFormation = this.verticalFormationFactory.create(),
			formationAggregate = this.formationAggregateFactory.create();

		const sorterContainer: SorterCollection = new SorterCollection(),
			filterContainer = this.filterManagerFactory.create(false),
			fieldContainer = this.fieldCollectionFactory.create(),
			aggregationManager = this.aggregationManagerFactory.create(structureId),
			schema = this.schemaAggregateFactory.create(),
			searchManager = this.searchManagerFactory.create();

		const structureAggregate = new StructureAggregate(structureId,
			paging,
			formationAggregate,
			source,
			verticalFormation,
			aggregationManager,
			sorterContainer,
			filterContainer,
			searchManager,
			fieldContainer,
			schema
		);

		this.init(structureAggregate);

		return structureAggregate;
	}

	init(structureAggregate: StructureAggregate): void {
		structureAggregate.init();
	}

}
