import { Injectable } from '@angular/core';

import { VerticalFormation } from '../../command/vertical-formation/vertical-formation';
import { VerticalFormationReadModel } from './vertical-formation.read-model';


@Injectable()
export class VerticalFormationConverter {

	convert(verticalFormation: VerticalFormation): VerticalFormationReadModel {

		const enabled = verticalFormation.isEnabled(),
			topMargin = verticalFormation.getTopMargin(),
			containerHeight = verticalFormation.calcFullHeight(),
			viewportHeight = verticalFormation.getViewPortHeight(),
			rowHeight = verticalFormation.getRowHeight();

		return new VerticalFormationReadModel(enabled, topMargin, containerHeight, viewportHeight, rowHeight);
	}

}
