import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../structure-id';
import { AggregatedValues } from './calculation/aggregated.values';


export class StructureAggregationsChangedEvent extends DomainEvent {

	constructor(structureId: StructureId,
				private readonly values: Map<string, AggregatedValues>) {
		super(structureId, 'StructureAggregationsChangedEvent');
	}

	getAggregations() {
		return this.values;
	}

}
