import { Injectable } from '@angular/core';

import { ColumnDefinitionFactory } from './read/definition/column-definition.factory';
import { CompositionAggregate } from './command/composition.aggregate';
import { Composition } from './read/composition';
import { ColumnEntity } from './command/column/column.entity';
import { ColumnDefinition } from './read/definition/column-definition';
import { ActiveColumnEntity } from './command/column/active/active-column.entity';


@Injectable()
export class CompositionConverter {

	constructor(private columnDefinitionFactory: ColumnDefinitionFactory) {
	}

	convert(aggregate: CompositionAggregate): Composition {

		const ready = aggregate.isReady(),
			allColumns: Array<ColumnEntity> = aggregate.getColumns(),
			activeColumns: Array<ActiveColumnEntity> = aggregate.getActiveColumns(),
			allColumnDefs: Array<ColumnDefinition> = this.convertToColumnDef(allColumns),
			activeColumnDefs: Array<ColumnDefinition> = this.convertActiveColumnsToColumnDef(activeColumns),
			width = aggregate.getWidth(),
			resizeWidth = aggregate.isResizeEnabled(),
			id = aggregate.getId();

		return new Composition(id, ready, allColumnDefs, activeColumnDefs, width, resizeWidth);
	}

	private convertToColumnDef(columns: Array<ColumnEntity>): Array<ColumnDefinition> {

		const columnDefs: Array<ColumnDefinition> = [];

		columns.forEach((column: ColumnEntity) => {

			const columnDef = this.columnDefinitionFactory.create(column);

			columnDefs.push(columnDef);
		});

		return columnDefs;
	}

	private convertActiveColumnsToColumnDef(activeColumns: Array<ActiveColumnEntity>): Array<ColumnDefinition> {

		const columnDefs: Array<ColumnDefinition> = [];

		activeColumns.forEach((column: ActiveColumnEntity) => {

			const columnDef = this.columnDefinitionFactory.create(column);

			columnDefs.push(columnDef);
		});

		return columnDefs;
	}

}
