import { Injectable, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { filter, map, take, takeUntil } from 'rxjs/operators';

import { SchemaDispatcher } from '../../domain/schema/command/schema.dispatcher';
import { SchemaReadModelService } from './schema-read-model.service';
import { StructureId } from '../../domain/structure-id';
import { globalStructureId } from '../../domain/global-structure-id';
import { SchemaTheme } from '../../domain/schema/schema-theme';
import { SchemaRowColoring } from '../../domain/schema/schema-row-coloring';
import { SchemaCssClass } from '../../domain/schema/read/schema-css-class';
import { SchemaColumnHeader } from '../../domain/schema/read/schema-column-header';


@Injectable()
export class SchemaCommandService implements OnDestroy {

	private unsubscribe$ = new Subject<void>();

	constructor(private schemaDispatcher: SchemaDispatcher,
				private schemaQueryService: SchemaReadModelService) {
	}

	ngOnDestroy() {
		this.unsubscribe$.next();
		this.unsubscribe$.complete();
	}

	setSchema(height: number, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.setSchema(structureId, height);
	}

	setHeight(height: number, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.setSchema(structureId, height);
	}

	changeVerticalGrid(enabled: boolean, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.changeVerticalGrid(structureId, enabled);
	}

	changeHorizontalGrid(enabled: boolean, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.changeHorizontalGrid(structureId, enabled);
	}

	setTheme(theme: SchemaTheme, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.setTheme(structureId, theme);
	}

	setRowColoring(coloring: SchemaRowColoring, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.setRowColoring(structureId, coloring);
	}

	changeTopHeader(enabled: boolean, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.changeTopHeader(structureId, enabled);
	}

	changeBottomHeader(enabled: boolean, structureId: StructureId = globalStructureId): void {
		this.schemaDispatcher.changeBottomHeader(structureId, enabled);
	}

	toggleVerticalGrid(structureId: StructureId = globalStructureId): void {
		this.schemaQueryService
			.onCssClasses(structureId)
			.pipe(
				map((css: SchemaCssClass) => css.verticalGrid),
				filter((grid: boolean) => grid !== undefined),
				take(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((grid: boolean) => {
				this.changeVerticalGrid(!grid, structureId);
			});
	}

	toggleHorizontalGrid(structureId: StructureId = globalStructureId): void {
		this.schemaQueryService
			.onCssClasses(structureId)
			.pipe(
				map((css: SchemaCssClass) => css.horizontalGrid),
				filter((grid: boolean) => grid !== undefined),
				take(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((grid: boolean) => {
				this.changeHorizontalGrid(!grid, structureId);
			});
	}

	toggleTopHeader(structureId: StructureId = globalStructureId): void {
		this.schemaQueryService
			.onColumnHeader(structureId)
			.pipe(
				map((columnHeader: SchemaColumnHeader) => columnHeader.isTopHeaderEnabled()),
				take(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((enabled: boolean) => {
				this.changeTopHeader(!enabled, structureId);
			});
	}

	toggleBottomHeader(structureId: StructureId = globalStructureId): void {
		this.schemaQueryService
			.onColumnHeader(structureId)
			.pipe(
				map((columnHeader: SchemaColumnHeader) => columnHeader.isBottomHeaderEnabled()),
				take(1),
				takeUntil(this.unsubscribe$)
			)
			.subscribe((enabled: boolean) => {
				this.changeBottomHeader(!enabled, structureId);
			});
	}

}
